import React from "react";
import { Typography, Button,Divider } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import ButtonBase from "@material-ui/core/ButtonBase";
import {} from "@material-ui/core";
import { Row } from "react-bootstrap";
import CountUp from 'react-countup';
const CoffeCard = (props) => {
  const { title,status, payment, career, imageUrl, view ,date } = props;
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      margin: "auto",
      maxWidth: 1600,
    },
    letter: {
      color:"white"
    },
   
    status: {
      backgroundColor:payment,
      color:"white",
      borderRadius:15, 
      boxShadow:3
    }
  }));
  const classes = useStyles();
  
  return (
    <div data-aos="zoom-in">
      <div className={classes.root}>
        <Paper className={classes.status}  elevation={3}>
          <Grid container spacing={2}>
            
            <Grid item xs={12} sm container>
              <Grid item xs container direction="column" spacing={0}>
                <Grid item xs>
                  <Typography  variant="h6" align="center">
                    {title}
                  </Typography>
                
                  </Grid>
                  <Grid item xs>
                  <Typography  variant="h5" align="center" style={{fontWeight:800}}>
                  <CountUp  start={0} end={view} duration={20} suffix="$/h"/>
                  </Typography>
                
                  </Grid>
                  </Grid>
                  </Grid>
                  </Grid>
        </Paper>
      </div>
    </div>
  );
};

export default CoffeCard;
