import React from "react";
import {
  // TextField,
  Grid,
  // Button,
  Box,
  // Divider,
} from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import CountrySelector from "Components/Offers/countrySelector";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
}));

export default function SimpleAccordion() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Delivery time</Typography>
        </AccordionSummary>

        <AccordionDetails>
          <Grid container direction="column">
            <Grid container direction="row" justify="left" alignItems="center">
              <input type="radio" value="Within 1 day" name="first" />{" "}
              <span style={{ marginLeft: 10 }}> Within 1 day</span>
            </Grid>

            <Grid
              container
              direction="row"
              justify="left"
              alignItems="center"
              style={{ marginTop: 7 }}
            >
              <input type="radio" value="Within 2 days" name="Within 2 days" />{" "}
              <span style={{ marginLeft: 10 }}> Within 2 days</span>
            </Grid>

            <Grid
              container
              direction="row"
              justify="left"
              alignItems="center"
              style={{ marginTop: 7 }}
            >
              <input type="radio" value="Within 2 days" name="Within 2 days" />{" "}
              <span style={{ marginLeft: 10 }}> Within 2 days</span>
            </Grid>

            <Grid
              container
              direction="row"
              justify="left"
              alignItems="center"
              style={{ marginTop: 7 }}
            >
              <input type="radio" value="Within 3 days" name="Within 3 days" />{" "}
              <span style={{ marginLeft: 10 }}> Within 3 days</span>
            </Grid>

            <Box
              boxShadow={2}
              style={{
                backgroundColor: "#FFF",
                height: 40,
                width: 150,
                borderRadius: 8,
                marginTop: 16,
                paddingTop: 7,
                paddingLeft: 16,
                marginLeft:60,
              }}
            >
              <span style={{ marginLeft: 10, marginTop: 5 }}>custom days</span>
            </Box>
          </Grid>
        </AccordionDetails>
      </Accordion>

      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>
            Service provider country
          </Typography>
        </AccordionSummary>

        <AccordionDetails style={{ marginBottom: 15 }}>
          <CountrySelector />
        </AccordionDetails>
      </Accordion>

      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Price</Typography>
        </AccordionSummary>

        <AccordionDetails>
          <Grid container direction="column">
            <Grid container direction="row" justify="left" alignItems="center">
              <input type="radio" value="Widthin 1 day" name="first" />{" "}
              <span style={{ marginLeft: 10 }}> Under $20</span>
            </Grid>

            <Grid
              container
              direction="row"
              justify="left"
              alignItems="center"
              style={{ marginTop: 7 }}
            >
              <input type="radio" value="Within 2 days" name="Within 2 days" />{" "}
              <span style={{ marginLeft: 10 }}> $20 to $50</span>
            </Grid>

            <Grid
              container
              direction="row"
              justify="left"
              alignItems="center"
              style={{ marginTop: 7 }}
            >
              <input type="radio" value="Within 2 days" name="Within 2 days" />{" "}
              <span style={{ marginLeft: 10 }}> $50 to $100</span>
            </Grid>

            <Grid
              container
              direction="row"
              justify="left"
              alignItems="center"
              style={{ marginTop: 7 }}
            >
              <input type="radio" value="Within 3 days" name="Within 3 days" />{" "}
              <span style={{ marginLeft: 10 }}> Over $100</span>
            </Grid>

            <Grid
              container
              direction="row"
              justify="center"
              alignItems="center"
              style={{ marginTop: 15 }}
            >
              <Box
                boxShadow={3}
                style={{
                  marginRight: 15,
                  backgroundColor: "#FFF",
                  height: 40,
                  width: 100,
                  borderRadius: 8,
                  paddingTop: 7,
                  paddingLeft: 16,
                }}
              >
                <span>$</span>
                <span style={{ marginLeft: 10, marginTop: 5 }}>min</span>
              </Box>

              <Box
                boxShadow={3}
                style={{
                  backgroundColor: "#FFF",
                  height: 40,
                  width: 100,
                  borderRadius: 8,
                  paddingTop: 7,
                  paddingLeft: 16,
                }}
              >
                <span>$</span>
                <span style={{ marginLeft: 10, marginTop: 5 }}>max</span>
              </Box>
            </Grid>
          </Grid>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
