import React from "react";
import { Box, Typography, Card } from "@material-ui/core";

import { CardMedia } from "@material-ui/core";
const CoffeCard = (props) => {
  const { title, imageUrl } = props;

  return (
    <div data-aos="zoom-in">
      <Box boxShadow={0}>
        <Card
          style={{
            borderRadius: 15,
            height: "auto",
            width: "auto",
            maxWidth: 200,
            maxHeight: "auto",
            minWidth: 92,
            minHeight: 160,
            margin: "auto",
          }}
        >
          <CardMedia
            style={{
              height: "auto",
              width: "auto",
              maxHeight: 100,
              minHeight: 80,
              maxWidth: 70,
              minWidth: 65,
              margin: "auto",
              marginTop: 10,
            }}
            image={imageUrl}
          />

          <Typography
            variant="h7"
            component="p"
            align="center"
            display="block"
            margin="auto"
          >
            <div
              data-aos="fade-up"
              style={{
                height: "auto",
                width: "auto",
                maxHeight: 30,
                minHeight: 70,
                maxWidth: 150,
                minWidth: 93,
                margin: "auto",
                fontSize: "auto",

                fontFamily: "Macan",
              }}
            >
              {title}
              <br />
              <br />
            </div>
          </Typography>
        </Card>
      </Box>
    </div>
  );
};

export default CoffeCard;
