// @ts-nocheck
import React from "react";
import {
    Grid,
    Divider,
    Avatar,
    Menu,
    MenuItem,
    Typography,
    Paper, Container
} from "@material-ui/core";
import { Button, Form, } from 'react-bootstrap';
import faq from "Assets/Images/CustomerSupport/faq.png";
import imgCustomerSupport from "Assets/Images/CustomerSupport/imgCustomerSupport.png";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import DashboardNavbar from "../dashboardNavbar";

export default function CustomerSupport() {
    const [value, setValue] = React.useState('ticket');

    const handleChange = (event) => {
        setValue(event.target.value);
    };


    return (
        <Grid container xs={12} style={{ display: "flex", justifyContent: "center" }}>
              <Grid item xs={12} sm={12} style={{ height: 60, background: "#FFF" }}>
                    <DashboardNavbar notDashboard />
                </Grid>

            <Grid item xs={10} style={{/* display:"flex", justifyContent:"center", */ backgroundColor: "white", height: "auto", margin: 50, borderRadius: 20 }}>

                <Grid item xs={12} style={{ display: "flex", justifyContent: "center", alignItems: "center", backgroundColor: "#0F4C81", height: 300, borderRadius: "20px 20px 0px 0px" }}>
                    <Grid item>

                        <Typography style={{ display: "flex", justifyContent: "center", fontSize: 35, color: "white", fontWeight: 700, fontFamily: "Roboto" }}>Customer Support</Typography>
                        <Typography style={{ display: "flex", justifyContent: "center", fontSize: 17, paddingBottom: 30, color: "white", fontWeight: 400, fontFamily: "Roboto" }}>Feel free to get in touch with us...</Typography>
                    </Grid>
                </Grid>

                <Grid item xs={12} style={{ display: "flex", justifyContent: "center" }}>
                    <Grid item xs={3} style={{ marginTop: -50,/*  display:"flex", justifyContent:"center" */ }}>
                        <Paper elevation={3} style={{ backgroundColor: "white", height: 280, borderRadius: 20, alignItems: "center" }}>

                            <Grid item xs={12} style={{ backgroundColor: "#edeef7", borderRadius: "20px 20px 0px 0px" }}>
                                <i className="fas fa-map-marker-alt" style={{ display: "flex", justifyContent: "center", fontSize: 40, paddingTop: 35, paddingBottom: 35, color: "#0F4C81" }} />
                            </Grid>

                            <Typography style={{ display: "flex", justifyContent: "center", paddingTop: 30 }}>
                                P.O Box- 194,Colts NeckNJ 07722
                            </Typography>
                            <Typography style={{ display: "flex", justifyContent: "center", paddingTop: 15 }}>
                                +1 (833) 662 5552
                            </Typography>
                            <Typography style={{ display: "flex", justifyContent: "center", paddingTop: 15 }}>
                                +1 (646) 226-5158
                            </Typography>

                        </Paper>
                    </Grid>


                    <Grid item xs={3} style={{ marginTop: -50, marginLeft: 30/*  display:"flex", justifyContent:"center" */ }}>
                        <Paper elevation={3} style={{ backgroundColor: "white", height: 280, borderRadius: 20, alignItems: "center" }}>

                            <Grid item xs={12} style={{ backgroundColor: "#edeef7", borderRadius: "20px 20px 0px 0px" }}>
                                <i className="fas fa-address-book" style={{ display: "flex", justifyContent: "center", fontSize: 40, paddingTop: 35, paddingBottom: 35, color: "#0F4C81" }} />
                            </Grid>


                            <Typography style={{ display: "flex", justifyContent: "center", paddingTop: 30 }}>
                                support@mtempz.com
                            </Typography>
                            <Typography style={{ display: "flex", justifyContent: "center", paddingTop: 15 }}>
                                contact@mtempz.com
                            </Typography>


                            <Grid item style={{ display: "flex", justifyContent: "center", paddingTop: 15 }}>
                                <i className="fab fa-facebook" style={{ margin: 10, fontSize: 23, color: "#0F4C81" }} />
                                <i className="fab fa-apple" style={{ margin: 10, fontSize: 23, color: "#0F4C81" }} />
                                <i className="fab fa-google" style={{ margin: 10, fontSize: 23, color: "#0F4C81" }} />
                                <i className="fab fa-twitter" style={{ margin: 10, fontSize: 23, color: "#0F4C81" }} />
                            </Grid>

                        </Paper>
                    </Grid>
                </Grid>







                <Grid item xs={12} style={{ display: "flex", justifyContent: "center", marginTop: -30 }}>
                    <img src={imgCustomerSupport} alt="faq" style={{ height: 500, display: "flex", justifyContent: "center", /* backgroundColor:"yellow" */ }} />
                </Grid>









                <Grid item xs={12} style={{ display: "flex", justifyContent: "center", marginTop: -40 }}>
                    <Grid item xs={8} style={{ padding: 50 }} >
                        <Paper elevation={3} style={{ backgroundColor: "white", height: "auto", borderRadius: 20, alignItems: "center", }}>
                            
                            
                            <Grid item xs={12} style={{backgroundColor: "#edeef7", borderRadius: "20px 20px 0px 0px",  height: 80,display: "flex", justifyContent: "center",alignItems:"center" }}>
                                <Typography style={{  fontSize: 32, fontWeight: 800,color:"#28527A" }}>Contact us</Typography>
                            </Grid>
 

                            <Form style={{padding: 50 }}>
                                <RadioGroup value={value} onChange={handleChange}>
                                    <Grid item xs={12} style={{ display: "flex", justifyContent: "center" }}>
                                        <FormControlLabel value="ticket" control={<Radio />} label="Support Ticket" style={{ marginRight: 50 }} />
                                        <FormControlLabel value="feedback" control={<Radio />} label="Send Feedback" />
                                    </Grid>
                                </RadioGroup>

                                <Form.Group controlId="formBasicName">
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control type="text" placeholder="Your Name" />
                                </Form.Group>

                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label>Email address</Form.Label>
                                    <Form.Control type="email" placeholder="Your Email" />
                                </Form.Group>

                                <Form.Group controlId="formBasicPassword">
                                    <Form.Label>Message</Form.Label>
                                    <Form.Control type="text" as="textarea" rows={6} placeholder="Your Message" />
                                </Form.Group>

                                <Grid xs={12} item style={{ display: "flex", justifyContent: "center", marginTop: 20 }}>
                                    <Button variant="danger" type="submit" style={{ width: 250 }}>
                                        <div style={{ fontSize: 16, fontWeight: 800 }}>Submit</div>
                                    </Button>
                                </Grid>

                            </Form>
                        </Paper>
                    </Grid>
                </Grid>




                <Grid item xs={12} style={{ display: "flex", justifyContent: "center", marginTop: 50 }}>
                    <Grid item xs={11}>
                        <Divider />
                    </Grid>
                </Grid>


                <Grid item xs={12} style={{ padding: 50, }}>
                    <Grid item xs={12} style={{ display: "flex", justifyContent: "center" }}>
                        <img src={faq} alt="faq" style={{ height: 100, display: "flex", justifyContent: "center" }} />
                    </Grid>
                    <Typography style={{ fontSize: 40, fontFamily: "Roboto", fontWeight: 900, display: "flex", justifyContent: "center", color: "#28527a" }}>FAQ</Typography>
                </Grid>




            </Grid>

        </Grid>
    );
}