import React, { useState, useCallback } from 'react';
import { render } from 'react-dom';
import ImageViewer from 'react-simple-image-viewer';
import {Grid} from "@material-ui/core"
export default function App() {
  const [currentImage, setCurrentImage] = useState(0);
  const [isViewerOpen, setIsViewerOpen] = useState(false);
  const images = [
    'https://cdn.americanprogress.org/content/uploads/2018/11/14135302/GettyImages-904850226.jpg',
    'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTYR_iqODBP7I7SXiTMPStAGtBmgwrVSAozNw&usqp=CAU',
    'https://raisingchildren.net.au/__data/assets/image/0023/56453/child-care-planning-preparing-wide.jpg',
    'https://www.verywellfamily.com/thmb/INGg8mGoFFEJhmsd3e2q1FnRFMk=/2122x1415/filters:no_upscale():max_bytes(150000):strip_icc()/Child-care-Christopher-Futcher-E-Plus-GettyImages-157726989-569fd55e3df78cafda9e8b9b.jpg',
    'https://cdn.americanprogress.org/content/uploads/2018/11/14135302/GettyImages-904850226.jpg',
    'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTYR_iqODBP7I7SXiTMPStAGtBmgwrVSAozNw&usqp=CAU',
    'https://raisingchildren.net.au/__data/assets/image/0023/56453/child-care-planning-preparing-wide.jpg',
    'https://www.verywellfamily.com/thmb/INGg8mGoFFEJhmsd3e2q1FnRFMk=/2122x1415/filters:no_upscale():max_bytes(150000):strip_icc()/Child-care-Christopher-Futcher-E-Plus-GettyImages-157726989-569fd55e3df78cafda9e8b9b.jpg',
  ];

  const openImageViewer = useCallback((index) => {
    setCurrentImage(index);
    setIsViewerOpen(true);
  }, []);

  const closeImageViewer = () => {
    setCurrentImage(0);
    setIsViewerOpen(false);
  };

  return (
    <div>
      {images.map((src, index) => (
        <img
          src={ src }
          onClick={ () => openImageViewer(index) }
          
         
          key={ index }
          style={{ margin: '1px',maxWidth:100,width:"auto",minWidth:100,maxHeight:100,minHeight:100,height:"auto" }}
          alt=""/>
      ))}

      {isViewerOpen && (
      <Grid   style={{ margin: '1px',maxWidth:100,width:100,minWidth:100,maxHeight:100,minHeight:100,height:"auto" }}> 
       <ImageViewer
          src={ images }
          currentIndex={ currentImage }
          onClose={ closeImageViewer }
    
        
        /></Grid>
      )}
    </div>
  );
}