// @ts-nocheck
import React from "react";
import { Grid, Divider } from "@material-ui/core";

import { Button } from "react-bootstrap";

import SettingsAvatar from "Components/Settings/settingsAvatar";
import SettingsTextField from "Components/Settings/settingsTextField";
import SettingsLinkedAccounts from "Components/Settings/settingsLinkedAccounts";
import SettingsDeleteAccount from "Components/Settings/settingsDeleteAccount";
import CustomizedSwitches from "Components/Settings/customizedSwitches";
import SettingsAlerts from "Components/Settings/settingsAlerts";
import CategoriesOfInterest from "Components/Settings/categoriesOfInterest";
import InterestCategory from "Components/Settings/interestCategory";

import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function SettingsNotificationBar() {
  function Spacer(props) {
    return (
      <Divider
        style={{
          maxWidth: 800,
          minWidth: 700,
          marginTop: props.top,
        }}
      />
    );
  }

  return (
    <Grid
      container
      spacing={2}
      justify="center"
      style={{
        marginLeft: 10,
        marginRight: 10,
        marginTop: -50,
        maxWidth: 800,
        minWidth: 600,
        height: "auto",
        backgroundColor: "#FFF",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: 15,
      }}
    >
      <Grid
        container
        justify="flex-start"
        style={{
          fontSize: 30,
          fontWeight: 700,
          marginTop: 50,
          marginLeft: 50,
        }}
      >
        Notification settings
      </Grid>

      <Grid style={styles.notificationGrid}>
        <SettingsAlerts />
        <Spacer top={25} />
        <CategoriesOfInterest />
      </Grid>

      <Grid
        container
        justify="center"
        style={{
          width: 650,
          height: "auto",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <InterestCategory label="CHILD CARE" isActive />
        <InterestCategory label="SENIOR CARE" />
        <InterestCategory label="SPECIAL NEEDS" />
        <InterestCategory label="PET CARE" />
        <InterestCategory label="TUTORING" />
        <InterestCategory label="HOUSE KEEPING" isActive />
        <InterestCategory label="LAWN SERVICES" />
        <InterestCategory label="HANDYMAN SERVICES" isActive />
        <InterestCategory label="POOL SERVICES" />
        <InterestCategory label="TRANSPORTATION SERVICES" />
        <InterestCategory label="SKIN HAIR & NAIL SERVICES" isActive />
        <InterestCategory label="EVENT SERVICES" />
        <InterestCategory label="REAL ESTATE" isActive />
      </Grid>
      <Spacer top={35} />

      <Grid
        container
        justify="space-between"
        style={{
          height: 25,
          maxWidth: 700,
          minWidth: 600,

          marginTop: 30,
          marginBottom: 50,

          display: "flex",
          justifyContent: "flex-end",
        }}
      >
        <Button variant="success">Save changes</Button>
      </Grid>
    </Grid>
  );
}
