import React from "react";
import { Grid, Divider } from "@material-ui/core";

import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function CategoriesOfInterest() {
  return (
    <Grid>
      <div style={styles.notificationGridTextStyles}>
        Categories of Interest
      </div>
      If we find some great deals in categories you like we'll update you on the
      latest or greatest.
      <div style={{ marginTop: 20 }}></div>
     
    </Grid>
  );
}
