// @ts-nocheck
import React from "react";
import { Grid, Divider } from "@material-ui/core";
import CustomizedSwitches from "Components/Settings/customizedSwitches";
import BillingTextInput from "Components/Settings/billingTextInput";
import SettingsCardNumber from "Components/Settings/settingsCardNumber";

import styles from "Assets/Styles/Settings/settingsStyles.js";

function PasswordValidation(props) {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "flex-start",
        marginTop: 20,
        marginLeft: 15,
      }}
    >
      <i className="far fa-check-circle" style={{color:"#16c79a"}}></i>
      <div style={styles.passwordValidation}>{props.label}</div>
    </div>
  );
}

export default function SettingsChangePassword() {
  return (
    <Grid
      style={{
        maxWidth: 800,
        minWidth: 600,
        display: "flex",
        justifyContent: "center",
      }}
    >
      <Grid xs={12} sm={12} style={styles.settingPagesSubheading}>
        Change Password
        <Grid style={{ marginTop: 15 }}>
          <PasswordValidation label ="Must be atleast 6 characters!"/>
          <PasswordValidation label ="Must contain atleast 1 number!"/>
          <PasswordValidation label ="Must contain atleast 1 in Captial Case!"/>
          <PasswordValidation label ="Must contain atleast 1 Letter in Small Case!"/>
          <PasswordValidation label ="Must contain atleast 1 Special Character!"/>
        </Grid>
      </Grid>
      <div style={{ marginTop: 20 }}></div>

      <Grid
        style={{
          maxWidth: 350,
          minWidth: 300,
        }}
      >
        <SettingsCardNumber
          title="OLD PASSWORD"
          placeholder="Nate Parrish"
          sm={12}
        />
        <div style={{ height: 15 }} />

        <SettingsCardNumber
          title="NEW PASSWORD"
          placeholder="Nate Parrish"
          sm={12}
        />
        <SettingsCardNumber
          title="CONFIRM NEW PASSWORD"
          placeholder="Nate Parrish"
          sm={12}
        />
      </Grid>
    
    
    
    </Grid>
  );
}
