import React from "react";
import "./search.css";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import InputBase from "@material-ui/core/InputBase";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import ArrowForwardOutlinedIcon from "@material-ui/icons/ArrowForwardOutlined";
import Select from "react-select";
const options = [
  { value: "Child Care", label: "Child Care" },
  { value: "Senior Care", label: "Senior Care" },
  { value: "Skin Care", label: "Skin Care" },
  { value: "Toturing", label: "Toturing" },
  { value: "House Keeping", label: "House Keeping" },
  { value: "Transport Services", label: "Transport Services" },
];

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "2px 4px",
    display: "flex",
    alignItems: "center",
    width: "auto",
    minWidth: 400,
    maxWidth: 1165,
    height: 60,
    margin: "auto",
    border: 10,
    borderBlockColor: "black",
    color: "white",
    boxShadow: 3,
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
  },
  divider: {
    height: 28,
    margin: 4,
  },
}));
const customStyles = {
  option: (provided, state) => ({
    ...provided,
    // borderBottom: "1px dotted pink",
    color: state.isSelected ? "whtie" : "black",
    // padding: 20,
  }),
  control: () => ({
    // none of react-select's styles are passed to <Control />
    width: 250,
  }),
  singleValue: (provided, state) => {
    const opacity = state.isDisabled ? 0.5 : 1;
    const transition = "opacity 300ms";

    return { ...provided, opacity, transition };
  },
  input: () => ({
    fontSize: 20,
    fontWeight: "bold",
  }),
};
export default function CustomizedInputBase() {
  const classes = useStyles();

  return (
    <Paper component="form" className={classes.root}>
      <InputBase
        className={classes.input}
        placeholder="Search any sevices"
        inputProps={{ "aria-label": "search google maps" }}
      />
      <IconButton
        type="submit"
        className={classes.iconButton}
        aria-label="search"
      >
        <MenuIcon />{" "}</IconButton>
        <div className="All"> 
      
          {" "}
          <Select
            components={{
              DropdownIndicator: () => null,
              IndicatorSeparator: () => null,
            }}
            isSearchable={true}
            placeholder="All Categories"
            styles={customStyles}
            options={options}
          />
        </div>
      <Divider className={classes.divider} orientation="vertical" />
      <IconButton
        color="primary"
        className={classes.iconButton}
        aria-label="directions"
      >
        <ArrowForwardOutlinedIcon
          style={{
            color: "white",
            background: "#f17855",
            height: 40,
            width: 50,
          }}
        />
      </IconButton>
    </Paper>
  );
}
