import React from "react";
import { Grid } from "@material-ui/core";

import { DropzoneArea } from "material-ui-dropzone";

export default function ProjectDropZone() {
  return (
    <Grid
      xs={12}
      sm={10}
      style={{
        width: "auto",
        // display: "flex",
        // justifyContent: "center",
        marginTop: 40,
      }}
    >
      <div
        style={{
          fontWeight: 900,
          color: "#000",
          fontSize: 12,
          marginBottom: 3,
        }}
      >
        {"UPLOAD SAMPLES AND OTHER HELPFUL MATERIAL (optional)"}
      </div>

      <div style={{ marginBottom: 20 }}>
        <DropzoneArea
          acceptedFiles={["image/*"]}
          dropzoneText={"                                 "}
          onChange={(files) => console.log("Files:", files)}
        />

        <div
          style={{
            marginTop: -150,
            position: "absolute",
            width: "auto",
            display: "flex",
            justifyContent: "center",
          }}
        >
          {"Drag and drop an images here"}
        </div>
        <div
          style={{
            marginTop: -110,
            position: "absolute",
            width: "auto",
            display: "flex",
            justifyContent: "center",
          }}
        >
          {"or"}
          <span style={{ color: "#ff0000",  }}>
            {" Browse "}
          </span>
          {"to add attachments"}
        </div>
      </div>
    </Grid>
  );
}
