import React from "react";

import { Grid } from "@material-ui/core";

// import { Button } from "react-bootstrap";

import styles from "Assets/Styles/Settings/settingsStyles.js";
import "Assets/Styles/Settings/settingsStyles.css";

export default function SettingsDeleteAccount() {
  return (
    <div style={styles.deleteAccount}>
      <Grid
        container
        justify="space-between"
        style={{
        
          marginTop: 15,
          marginRight: 50,
        }}
      >
        <div>
          <div style={styles.linkedAccountTitle}>Delete account</div>
          <Grid style={styles.linkedAccountDesc}>
            By deleting your account you will lose all your data
          </Grid>
        </div>

        <div
          className="deleteAccount"
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "flex-end",
            height: 35,
            marginTop: 8,
            fontWeight: 700,
          }}
        >
          <button type="button" className="deleteAccount">
            Delete account...
          </button>

          
        </div>
      </Grid>
    </div>
  );
}
