import React from "react";
import {
  TextField,
  Grid,
  //   Button,
  // Box,

  //   InputBase,
  // Divider,
  // Collapse,
} from "@material-ui/core";

export default function SettingsTextField(props) {
  return (
    <Grid style={{ width: 300 }}>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          fontSize: 11,
          marginLeft: 5,
          marginRight: 5,
        }}
      >
        <div style={{ fontWeight: 900, color: "#000", fontSize: 12 }}>
          {props.title}
        </div>
        <div style={{ fontWeight: 600, color: "#9D9D9D" }}>{props.desc}</div>
      </div>

      <TextField
        hiddenLabel={true}
        id="outlined-basic"
        //   label="Display Name"
        placeholder={props.placeholder}
        variant="outlined"
        size="small"
        style={{ width: 300, borderColor: "#A4A4A4" }}
      />
    </Grid>
  );
}
