import React from "react";
import { Typography, Paper,Divider } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import AllFilter from "./Filter/allType";
import ButtonBase from "@material-ui/core/ButtonBase";
import {} from "@material-ui/core";
import { Row } from "react-bootstrap";
import TotalEarningGraph from "./Graphs/totalEarningGraph"
import EarningGraph from "./Graphs/earningGraph"
import EarningPieGraph from "./Graphs/earningPieChart"
import EarningPerHour from "./EarningPerHour/CardView"



const CoffeCard = (props) => {
  const { title,status, payment, career, imageUrl, view ,date } = props;
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      margin: "auto",
      maxWidth: 1600,
    },
    image: {
      width: 128,
      height: 128,
    },
    img: {
      margin: "auto",
      display: "",
      maxWidth: "100%",
      maxHeight: "100%",
    },
    status: {
      color:payment
    }
  }));
  const classes = useStyles();
  
  return (
    <div data-aos="zoom-in">
      <div className={classes.root}>
        
          <Grid container spacing={2}>
            <Grid item></Grid>
            <Grid item xs={12} sm={10}container>
              
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs container direction="column" spacing={2} >
                <Grid item xs   style={{marginTop:35,marginLeft:10}}>
                  <Typography gutterBottom variant="h6"  >
                    Earning Per Hour Category Wise  
                </Typography>
                </Grid>
                <Grid item xs>
                <EarningPerHour/>
                </Grid>

              </Grid>         

              <Grid item   style={{marginInlineStart:5 ,backgroundColor:"",borderRadius:15,marginRight:5,marginLeft:5}}            
><br/>
<Paper             elevation={3}
 style={{boxShadow:10}}><br/>
<Typography gutterBottom variant="h6"  style={{margin:"auto", marginLeft:10}}>
                   Total  Earning Summary  
                </Typography>
<TotalEarningGraph /></Paper>
              </Grid>
              <Grid item   style={{marginInlineStart:5 ,backgroundColor:"",borderRadius:15,marginRight:5,marginLeft:5}}            
><br/>
<br/>
<Paper             elevation={3}
 style={{boxShadow:10}}><br/>
<Typography gutterBottom variant="h6"  style={{margin:"auto", marginLeft:10}}>
                     Earning Summary  
                </Typography>
<EarningGraph /></Paper>         </Grid>



            </Grid>
          </Grid>
        <Grid item> <Divider orientation="vertical"/></Grid> 
          <Grid item>
          <Paper             elevation={3}
 style={{boxShadow:10}}><br/>
<Typography  variant="h7"  style={{margin:"auto",marginleft:5,marginRight:5}}>
                    Current Project   
                </Typography>
                <Typography gutterBottom variant="h6" align="center"  style={{color:"gray"}}>
                   02
                </Typography>
{/* <EarningPieGraph /> */}
</Paper>
              </Grid>
              
          </Grid>
      </div>
    </div>
  );
};

export default CoffeCard;
