import React from "react";

import { Grid } from "@material-ui/core";

import { Button } from "react-bootstrap";

import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function SettingsLinkedAccounts() {
  return (
    <div style={styles.linkedAccount}>
      <div style={styles.linkedAccountTitle}>Linked Accounts</div>
      <Grid style={styles.linkedAccountDesc}>
        We use this to let you sign in and populate your profile information
      </Grid>

      <Grid
        container
        justify="space-between"
        style={{
          height: 25,
          maxWidth: 800,
          minWidth: 600,
          marginTop: 15,
        }}
      >
        <Button
          variant="light"
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <img
            src="https://cdn.icon-icons.com/icons2/2119/PNG/512/google_icon_131222.png"
            alt="googleIcon"
            style={{ height: 20, width: 20 }}
          ></img>
          <div style={{ marginLeft: 8 }}>Sign in with Google</div>
        </Button>

        <Button
          variant="outline-info"
          style={{ display: "flex", justifyContent: "flex-end" }}
        >
          Connect
        </Button>
      </Grid>
    </div>
  );
}
