import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { Grid,Container,Paper,Typography } from "@material-ui/core";
import Content from "./Content";


function  App()
{
 
AOS.init();
  return (
    <div>      <br />
     <Grid container direction="row" spacing={1} >
     <Grid item xs={false} sm={1} />
<Grid item xs={12} sm={10}><Paper elevation="3" style={{margin:10,backgroundColor:"",borderRadius:10}}>
<Grid container direction="row" spacing={2}>
<Grid item xs={12} sm={8}><Typography variant="caption" display="block"  gutterBottom align="center" style={{fontWeight:700,margin:10,color:"#1565c0",fontSize:30,marginTop:40}}> 
     View Your  Past Projects Here
      </Typography></Grid> 
      <Grid item xs={12} sm={4}>    <img
      src="https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2020/03/Networking-online.jpg?w=7500"
    width="200" height="140"/></Grid></Grid></Paper>
  </Grid>    
 <Grid item xs={false} sm={1} /></Grid> 
           
      <br/>
      <Grid container direction="column">
        <Grid item></Grid>
        <Grid item container>
          <Grid item xs={false} sm={1} />
          <Grid item xs={12} sm={10}>
            <Container maxWidth="xl">
              <Content />
            </Container>
          </Grid>
          <Grid item xs={false} sm={1} />
        </Grid>
      </Grid><br/>
      <Grid item xs={12} sm={12} align="center">    <img
      src="https://image.freepik.com/free-vector/happy-freelancer-with-computer-home-young-man-sitting-armchair-using-laptop-chatting-online-smiling-vector-illustration-distance-work-online-learning-freelance_74855-8401.jpg"
    width="240" height="160"/></Grid>
    </div>
  );

}

export default App;