// @ts-nocheck
import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import "bootstrap/dist/css/bootstrap.min.css";
import reportWebVitals from "./reportWebVitals";
import AOS from "aos";
import "aos/dist/aos.css";

import SignIn from "View/SignIn/signIn";
import SignupFirst from "View/SignUp/signupFirst";
import SignupSecond from "View/SignUp/signupSecond";
import SignupThird from "View/SignUp/signupThird";
import PreDashboard from "View/PreDashboard/preDashboard";
import Offers from "View/Offers/offers";
import ViewOffer from "View/ViewOffer/viewOffer";
import Settings from "View/Settings/settings";
import SettingsNotification from "View/Settings/settingsNotification";
import SettingsPayments from "View/Settings/settingsPayments";
import SettingsPrivacy from "View/Settings/settingsPrivacy";

import App from "App";
import Footer from "View/Footer/footer";
import FreeLancer from "View/FreeLancer/freeLancer";
import HireForm from "View/FreeLancer/HireForm/hireForm";
import { Route, BrowserRouter as Router, Switch, Redirect } from "react-router-dom";
import Profile from "View/FreeLancer/Profile/profile";
import Tab from "View/FreeLancer/Profile/tab";
import PostProject from "View/PostProject/postProject";
import Dashboard from "View/Dashboard/dashboard";
import SDashboard from "View/Sample/Dashboard/dashbord";
import Payments from "View/Payments/payments";
import KnowledgeBase from "View/KnowledgeBase/knowledgeBase";
// import sideTab from "./Components/DashBoard/ProposalsPage/CardView";
import sideTab from "./Components/DashBoard/sideTab";
import ViewProposal from "./Components/DashBoard/ViewProposal/viewProposal";
import CustomerSupport from "./Components/DashBoard/CustomerSupport/customerSupport";
import RequestViewPage  from "./Components/DashBoard/RequestPage/requestViewPage";
import MessageCenter  from "./Components/DashBoard/MessageCenter/messageCenter";
import LeaveFeedback  from "./Components/DashBoard/PastProjects/leaveFeedback";
import Dash from "./Components/DashBoard/MainDashboard/maindashboardRes"
import { createStore } from "redux";
import { Provider } from 'react-redux';
import allStates from "States";

const store = createStore(
  allStates,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

AOS.init();
ReactDOM.render(
  <React.StrictMode>
    <Provider store ={store}>
    <div style={{ background: "#EEECF0" }}>
      <Router>
        <Switch>
        <Route path="/KnowledgeBase" component={KnowledgeBase} />
        <Route path="/LeaveFeedback" component={LeaveFeedback} />
        <Route path="/Payments" component={Payments} />
        <Route path="/MessageCenter" component={MessageCenter} />
        <Route path="/Dash" component={Dash} />
          <Route path="/CustomerSupport" component={CustomerSupport} />
          <Route path="/ViewProposal" component={ViewProposal} />
          <Route path="/RequestViewPage" component={RequestViewPage} />

            <Route path="/SDashboard" component={sideTab} />
            {/* <Route path="/Dashboard/" render={(props) => <Dashboard {...props}/>} /> */}
            <Route path={"/Dashboard/:page"} render={(props) => <Dashboard {...props} />}>
              <Redirect to="/Dashboard" />
            </Route>
            <Route path={"/Dashboard"} component={Dashboard} />
            <Route path="/SettingsPrivacy" component={SettingsPrivacy} />
            <Route path="/PostProject" component={PostProject} />
            <Route path="/SettingsPayments" component={SettingsPayments} />
            <Route
              path="/SettingsNotification"
              component={SettingsNotification}
            />
            <Route path="/Settings" component={Settings} />
            <Route path="/ViewOffer" component={ViewOffer} />
            <Route path="/Tab" component={Tab} />
            <Route path="/Profile" component={Profile} />
            <Route path="/HireForm" component={HireForm} />
            <Route path="/Offers" component={Offers} />
            <Route path="/PreDashboard" component={PreDashboard} />
            <Route path="/Freelancer" component={FreeLancer} />
            <Route path="/CreateAccount" component={SignupFirst} />
            <Route path="/VerifyMobile" component={SignupSecond} />
            <Route path="/CreatedSuccess" component={SignupThird} />
            <Route path="/SignIn" component={SignIn} />
            <Route path="/" component={App} />


          </Switch>
        </Router>

        <Footer />
      </div>
    </Provider>
  </React.StrictMode>,

  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();