// @ts-nocheck
import React from "react";
import { Grid, Divider } from "@material-ui/core";
import CustomizedSwitches from "Components/Settings/customizedSwitches";
import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function SettingsAlerts() {
  return (
    <Grid>
      <div style={styles.notificationGridTextStyles}>Alerts</div>
      General email alerts, we promise we won't spam you!
      <div style={{ marginTop: 20 }}></div>
      <CustomizedSwitches label="Email alerts for hot deals based on your search history" />
      <CustomizedSwitches label="Email alerts on price drops for your wishlisted products" />
      <CustomizedSwitches label="Program updates and important announcements" />
    </Grid>
  );
}

function Spacer(props) {
  return (
    <Divider
      style={{
        maxWidth: 800,
        minWidth: 700,
        marginTop: props.top,
      }}
    />
  );
}
