import React from "react";
import { Grid } from "@material-ui/core";
import styles from "Assets/Styles/PreDashboard/preDashboardStyles.js";
import "Assets/Styles/PreDashboard/preDashboardStyles.css";
import BrowseOffers from "Components/PreDashboard/browseOffers";
import PostProject from "Components/PreDashboard/postProject";
import imgOffers from "Assets/Images/preDashboard/imgOffers.svg";
import imgFreelancers from "Assets/Images/preDashboard/imgFreelancers.svg";

import Navbar from "View/Appbar/navbar";


export default class PreDashboard extends React.Component {
  render() {
    return (
      <div>
        <Navbar />
        <div style={styles.root}>
          <Grid
            container
            direction="column"
            justify="center"
            alignItems="center"
          >
            <h4>Welcome, Amelia.</h4>
            <h2 style={{ paddingBottom: 15 }}>Make a selection, what your need</h2>

            <Grid
              container
              direction="row"
              justify="center"
              alignItems="center"
              spacing={1}
              xm={12} sm={12}

            >
              <BrowseOffers
                title="Browse Jobs"
                className="BrowseBox"
                description="Search works offered at a fixed price from freelancers"
                img={imgFreelancers}
                link="/Offers"
              />

              <BrowseOffers
                title="Browse Service Provider"
                className="BrowseBox"
                description="Search thousands of freelancer and contact and hire them directly."
                img={imgOffers}
                link="/Freelancer"
              />
            </Grid>
            <div style={{ height: 30 }} />
            <PostProject />
          </Grid>
        </div>
      </div>
    );
  }
}
