import React from "react";
import {
  /* TextField,
  Grid,
  Button,
  Box,
  Divider */
} from "@material-ui/core";

export default function OfferCardTag(props) {
  return (
    <div
      className="tags"
      style={{
        // backgroundColor: "#F8F4F3",
        backgroundColor: "#F6F7FC",
        color: "#625261",
        borderRadius: 5,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        fontSize: 10,
        fontFamily: "Roboto",
        fontWeight: 500,
        margin: 5,
        padding: 5,
      }}
    >
      {props.name}
    </div>
  );
}
