import React from "react";
import { Box, Typography, Card } from "@material-ui/core";

import { CardMedia } from "@material-ui/core";
function CoffeCard (props)  {
//   const { title, imageUrl } = props;

  return (
    <div data-aos="zoom-in">
      <Box boxShadow={0}>
        <Card
          style={{
            borderRadius: 15,
            height: "auto",
            width: "auto",
            maxWidth: 200,
            maxHeight: "auto",
            minWidth: 92,
            minHeight: 160,
            margin: "auto",
          }}
        >
          <CardMedia
            style={{
              height: "auto",
              width: "auto",
              maxHeight: 45,
              minHeight: 45,
              maxWidth: 45,
              minWidth: 44,
              margin: "auto",
              marginTop: 30,
              marginBottom:10
            }}
            image="https://cdn1.iconfinder.com/data/icons/general-9/500/add-512.png"
          />

          <Typography
            variant="h7"
            component="p"
            align="center"
            display="block"
            margin="auto"
          >
            <div
              data-aos="fade-up"
              style={{
                height: "auto",
                width: "auto",
                maxHeight: 30,
                minHeight: 70,
                maxWidth: 150,
                minWidth: 93,
                margin: "auto",
                fontSize: "auto",

                fontFamily: "Macan",
              }}
            >
              Customize Service
              <br />
              <br />
            </div>
          </Typography>
        </Card>
      </Box>
    </div>
  );
};

export default CoffeCard;
