import React from "react";
import {Grid,Typography} from "@material-ui/core";
import {  Row  } from "react-bootstrap";
import styles from "Assets/Styles/Settings/settingsStyles.js";
import "Assets/Styles/Settings/settingsStyles.css";
import SettingsSideBar from "Components/Settings/settingsSideBar";
import SettingsMainBar from "Components/Settings/settingsMainBar";
import Navbar from "View/Appbar/navbar";
export default class Settings extends React.Component {
  
  render() {
    return (
      <div>
        <Navbar />

        <Grid item container direction="row"
          justify="center">
          <Grid item xs={12} sm={10}  alignItems="center"
          justify="center">
          <SettingsMainBar />
          </Grid>
          <Grid item xs={12} sm={2} alignItems="center"
          justify="center">
          <Typography  style={{fontSize: 35,fontWeight: 700}} align="center">
                {"Settings "}
              </Typography>
              <Grid item justify="center"> <SettingsSideBar account /></Grid>
          </Grid>

        </Grid>
      </div>
    );
  }
}
