// @ts-nocheck
import React from "react";
import { Grid, Typography, Paper } from "@material-ui/core";
import styles from "Assets/Styles/Dashboard/dashboardStyles";

export default function ScheduleEventsCard(props) {
  return (
    <Grid
      item
      xs={12}
      style={{
        display: "flex",
        justifyContent: "center",
      }}
    >
      <Grid
        item
        xs={11}
        style={{
          marginTop: 20,
        //   marginBottom: 20,
        }}
      >
        <Paper
          style={{
            backgroundColor: "white",
          }}
        >
          <Grid
            container
            xs={12}
            justify="center"
            style={{ height: 160, fontFamily: "Roboto" }}
          >
            {/* Card Start */}

            <Grid
              xs={12}
              item
              style={{
                height: 30,
                marginTop: 10,
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <Grid
                xs={12}
                item
                style={{
                  display: "flex",
                  justifyContent: "flex-start",
                }}
              >
                <Typography style={styles.scheduleCardOngoing}>
                  {props.ongoing}
                </Typography>
                <Typography style={styles.scheduleCardOngoingOthers}>
                  {props.daily}
                </Typography>
              </Grid>

              <div
                style={{
                  height: 29,
                  width: 30,
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  marginRight: 20,
                }}
              >
                <i className="fas fa-ellipsis-h" style={{ fontSize: 20, color:"#B7B7B7" }} />
              </div>
            </Grid>

            <Grid item xs={11} style={{ height: 100, /* backgroundColor: "grey"  */}}>
              <Typography style={{ fontSize: 20, fontWeight: 700, marginTop:8, color:"#006a71" }}>
                {props.title}
              </Typography>
              <Typography style={{ fontSize: 15, fontWeight: 500 }}>
                {props.address}
              </Typography>


              <Grid
                item
                xs={11}
                style={{ marginTop: 8}}
              >
                <Typography style={{ fontSize: 15, fontWeight: 500 , display:'flex', justifyContent:'flex-start', alignItems:'center'}}>
                < i className="far fa-clock" style={{marginRight:5}}/>
                  <div>{props.time}</div>
                  <div style={{marginLeft: 5, fontWeight:600, color: props.isToday ? "#FF0000" : "#fb7813"}}>{props.isToday ? "Today": props.isTomorrow ? "Tommorrow": props.date}</div>
                </Typography>
              </Grid>


            </Grid>
          </Grid>
        </Paper>
      </Grid>
    </Grid>
  );
}
