import React from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar';
import { Grid, Typography } from "@material-ui/core";
import CountUp from 'react-countup';

export default function ReviewCard(props){
    return (
        <Grid item xs={12} style={{ display: "flex", justifyContent: "center", alignItems: "center", height: 20,  marginTop: 15, marginBottom: 15 }}>
        <Grid container xs={8} direction="row" style={{ display: "flex", alignItems: "center" }}>

            <Grid item xs={4} style={{ display: "flex", justifyContent: "flex-end", paddingRight: 10 }}>

                <Typography style={{ fontFamily: "Roboto", fontWeight: 700, color: "#323357", fontSize: 15 }}> {props.labelText}</Typography>
            </Grid>

            <Grid item xs={4}>
                <ProgressBar now={props.percentage < 5 ? props.percentage * 5 : props.percentage < 50 ? props.percentage * 3 : props.percentage } srOnly animated variant="warning" style={{ backgroundColor: "#F5F8FF" }} />
            </Grid>


            <Grid item xs={2}>

                <Typography style={{ fontFamily: "Roboto", fontWeight: 500, paddingLeft: 10, color: "#525371", fontSize: 14 }}>   <CountUp  start={0} end={props.percentage} duration={20} suffix="%"/></Typography>
            </Grid>

        </Grid>
    </Grid> 
    );
}