// @ts-nocheck
import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import MuiAccordion from '@material-ui/core/Accordion';
import MuiAccordionSummary from '@material-ui/core/AccordionSummary';
import MuiAccordionDetails from '@material-ui/core/AccordionDetails';
import "Assets/Styles/KnowledgeBase/knowledgeBaseStyles.css";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import KnowledgeBaseData from "Data/KnowledgeBase/knowledgeBaseData";
import {
    Grid,
    Divider,
    Typography,
} from "@material-ui/core";


const Accordion = withStyles({
    root: {
        border: '1px solid rgba(0, 0, 0, .125)',
        boxShadow: 'none',
        '&:not(:last-child)': {
            borderBottom: 0,
        },
        '&:before': {
            display: 'none',
        },
        '&$expanded': {
            margin: 'auto',
        },
    },
    expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
    root: {
        backgroundColor: 'rgba(0, 0, 0, .03)',
        borderBottom: '1px solid rgba(0, 0, 0, .125)',
        marginBottom: -1,
        minHeight: 56,
        '&$expanded': {
            minHeight: 56,
        },
    },
    content: {
        '&$expanded': {
            margin: '12px 0',
        },
    },
    expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
    },
}))(MuiAccordionDetails);








export default function KnowledgeAccordion() {

    const accordionList = (accordionObj) => {
        return <KnowledgeBaseCard {...accordionObj} />;
    };


    return (
        <div>
            {KnowledgeBaseData.map((accordionObj) =>
                accordionList(accordionObj)
            )}
        </div>
    );
}


function KnowledgeBaseCard(props) {

    const [expanded, setExpanded] = React.useState('q1');

    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };


    return (


        <Accordion square expanded={expanded === props.id} onChange={handleChange(props.id)} style={{ marginBottom: 8, color: "#0A615A" }}>

            <AccordionSummary expandIcon={<ExpandMoreIcon style={{ color: expanded == props.id ? "white" : "" }} />} aria-controls="panel1d-content" id="panel1d-header" className="title" style={{ backgroundColor: expanded == props.id ? "#4b778d" : "", color: expanded == props.id ? "white" : "" }}>
                <div style={{fontSize:expanded == props.id ? 19 : 17, fontWeight: expanded == props.id ? 700:600
                
                }}>{props.title }</div>
            </AccordionSummary>

            <AccordionDetails  >
                <div style={{color:"black"}}>
                    {props.body}
                </div>
            </AccordionDetails>

        </Accordion>

    );
}