// @ts-nocheck
import React from "react";
import { Grid, Divider } from "@material-ui/core";
import CustomizedSwitches from "Components/Settings/customizedSwitches";
import BillingTextInput from "Components/Settings/billingTextInput";
import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function SettingsBillingAddress() {
  return (
    <Grid style={{ minWidth: 600, marginTop: 40 }}>
      <Grid
        xs={12}
        sm={10}
        style={
          styles.settingPagesSubheading
        } /* style={styles.notificationGridTextStyles} */
      >
        Billing Address
      </Grid>
      {/* General email alerts, we promise we won't spam you! */}
      <div style={{ marginTop: 20 }}></div>
      <BillingTextInput title="FULL NAME" placeholder="Nate Parrish" />
      <BillingTextInput title="ADDRESS 1" placeholder="158  Scott Street" />
      <BillingTextInput title="ADDRESS 2" placeholder="41  Exning Road" />
      <BillingTextInput title="CITY" placeholder="HARGRAVE" />
      <BillingTextInput title="STATE" placeholder="Georgia" />
      <BillingTextInput title="ZIP CODE" placeholder="NN9 5WY" />
    </Grid>
  );
}
