import * as React from 'react';
import Paper from '@material-ui/core/Paper';
import {
  Chart,
  BarSeries,
  Title,
  ArgumentAxis,
  ValueAxis,
} from '@devexpress/dx-react-chart-material-ui';

import { Animation } from '@devexpress/dx-react-chart';

const data = [
  { year: '03.17', population: 15 },
  { year: '03.18', population: 19 },
  { year: '03.19', population:28 },
  { year: '03.20', population: 4 },
  { year: '03.21', population: 45 },
  { year: '03.22', population: 27 },
  { year: '03.23', population: 55 },
];

export default class Demo extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      data,
    };
  }

  render() {
    const { data: chartData } = this.state;

    return (
      <Paper>
        <Chart
          data={chartData}
        >
          <ArgumentAxis />
          <ValueAxis max={7} />

          <BarSeries
            valueField="population"
            argumentField="year"
          />
          <Title text="User Time Spend Graph" />
          <Animation />
        </Chart>
      </Paper>
    );
  }
}
