import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import AOS from "aos";
import "aos/dist/aos.css";
import { Grid, Container, Typography, Paper, Divider } from "@material-ui/core";
import ScheduleEventsHeadings from "Components/DashBoard/MainDashboard/scheduleEventsHeadings";
import ScheduleEventsCard from "Components/DashBoard/MainDashboard/scheduleEventsCard";
import scheduleEventsData from "Data/Dashboard/scheduleEventsData";
import DoughnutChart from "./doughnutChart"
import moment from "moment";
import ReviewBox from "./reviewBox"

const ScheduleEventsList = (scheduleEventsObj) => {
  return <ScheduleEventsCard {...scheduleEventsObj} />;
};

function MainDashboard() {
  AOS.init();
  return (
    <Grid
      xs={12}
      container
      style={{
        display: "flex",
        justifyContent: "center",
      }}
    >
      <Grid
        xs={11}
        container
        style={{
          //   backgroundColor: "grey",
          display: "flex",
          justifyContent: "flex-end",
          marginTop: 30,
        }}
      >
        <Grid xs={7} item >

          <Paper
            elevation={3}
            style={{
              backgroundColor: "#F9F8FD",
              display: "flex", justifyContent: "flex-start"
            }}
          >
            <Grid item xs={5}>

              <Grid item style={{ backgroundColor: "#FFFFFF", height: 250, alignItems: "center", display: "flex", justifyContent: "center" }}>
                <Grid item>

                  <Grid item xs={6} style={{}}>
                    <img src="https://i.pinimg.com/originals/be/ac/96/beac96b8e13d2198fd4bb1d5ef56cdcf.jpg" alt="profile pic" style={{ height: 140, width: 140, borderRadius: "50%", }} />
                  </Grid>


                  <Typography style={{ fontSize: 25, fontFamily: "Roboto", fontWeight: 900, color: "#435560" }}>
                    Amelia Grey
              </Typography>
                  <Typography style={{ fontSize: 14, fontFamily: "Roboto", fontWeight: 600, color: "#435560" }}>
                    ameliagrey@gmail.com
              </Typography>
                </Grid>
              </Grid>
            </Grid>



            <Grid item xs={7} style={{ fontSize: 20, marginTop: 30,  display: "flex", justifyContent: "center" }}>

              <Grid item xs={10}>

                <Grid xs={12} item style={{/* display: "flex", justifyContent: "center" */marginTop: 20 }} >
                  <Divider />
                </Grid>

                <Grid item style={{ display: "flex", justifyContent: "center", marginTop: 20 }}>
                  <Typography style={{ fontSize: 25, fontFamily: "Roboto", fontWeight: 900, color: "#bd2000" }}>
                    BASIC MEMBER
              </Typography>
                </Grid>

                <Grid item style={{ display: "flex", justifyContent: "center", }}>
                  <Typography style={{ fontSize: 15, fontFamily: "Roboto", fontWeight: 600, color: "#fa1e0e" }}>
                    upgrade now
              </Typography>
                </Grid>


                <Grid xs={12} item style={{/* display: "flex", justifyContent: "center" */marginTop: 20 }} >
                  <Divider />
                </Grid>



                <Grid item style={{ fontSize: 20, display: "flex", justifyContent: "center", marginTop: 36 }}>
                  <Typography style={{ fontSize: 16, fontFamily: "Roboto", fontWeight: 600, color: "#435560" }}>
                    120+ Successful Projects
              </Typography>
                </Grid>


              </Grid>
            </Grid>




          </Paper>


          <Paper
            elevation={3}
            style={{
              // backgroundColor: "#F9F8FD",
              backgroundColor: "#FFFFFF",
              marginTop: 20,
            }}
          >
            <Typography style={{ fontSize: 16, fontFamily: "Roboto", fontWeight: 800, color: "#495464", paddingBottom: 8, paddingTop: 15, paddingLeft: 20 }}>Top 5 Job Experiences</Typography>
            <Grid xs={12} item>
              <Divider />
            </Grid>
            <DoughnutChart />
          </Paper>








          <Paper
            elevation={3}
            style={{
              // backgroundColor: "#F9F8FD",
              backgroundColor: "#FFFFFF",
              marginTop: 20,
            }}
          >

            <Grid xs={12} container direction="row">

            <Grid xs={10} item style={{ display:"flex",alignItems:"center"}}>
            <Typography style={{ fontSize: 16, fontFamily: "Roboto", fontWeight: 800, color: "#495464", paddingBottom: 8, paddingTop: 15, paddingLeft: 20, alignItems:"center" }}>What Do Your Clients Really Think?</Typography>
            </Grid>

            <Grid xs={2} item style={{display:"flex", justifyContent:"center", alignItems:"center"}}>          
               <Typography style={{marginRight: 5, fontSize: 12,fontFamily: "Roboto", fontWeight: 800,display:"flex", justifyContent:"center", alignItems:"center" }}>
                  View all
               </Typography>
            </Grid>

            </Grid>



            <Grid xs={12} item>
              <Divider />
            </Grid>


            <Grid item xs={12} style={{height: "auto"}}>

            <ReviewBox/>


            </Grid>
          </Paper>










        </Grid>



        <Grid
          xs={5}
          container
          style={{
            display: "flex",
            justifyContent: "center",
            // backgroundColor: "#EDECFA",
          }}
        >
          <Grid item >
            <Paper>
              <ScheduleEventsHeadings />

              <Grid>
                {scheduleEventsData.map((scheduleEventsObj) =>
                  ScheduleEventsList(scheduleEventsObj)
                )}
              </Grid>



            
            </Paper>
          </Grid>
        </Grid>
      </Grid>    </Grid>
  );
}

export default MainDashboard;
