import React from "react";
import { Grid, TextField } from "@material-ui/core";

export default function SettingsCardNumber(props) {
  return (
    <Grid
      sm={props.sm}
      spading={2}
      container
      style={{
        fontSize: 11,
        margin: "auto",
        marginTop: 15,
        marginBottom: 15,
      }}
    >
      <div
        style={{
          fontWeight: 900,
          color: "#000",
          fontSize: 12,
          marginBottom: 3,
        }}
      >
        {props.title}
      </div>

      <TextField
        hiddenLabel={true}
        id="outlined-basic"
        placeholder={props.placeholder}
        variant="outlined"
        size="small"
        fullWidth
        style={{ borderColor: "#A4A4A4" }}
      />
    </Grid>
  );
}
