import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { TextField, Grid, Button } from "@material-ui/core";

import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";

import imageSignIn from "Assets/Images/loginSignupPage/imgSignup.svg";
import styles from "../../Assets/Styles/loginSignup/loginSignupStyles.js";
import "Assets/Styles/loginSignup/loginSignupStyles.css";
import Navbar from "View/Appbar/navbar";
import commonStyles from "Assets/Styles/loginSignup/commonStyles";

import { Link as Link1 } from "react-router-dom";

const useStyles = makeStyles(styles);

export default function SignIn() {
  const customStyles = useStyles();
  return (
    <div>
      <Navbar />
      <div className={customStyles.root} style={{ background: "#EEECF0" }}>
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="center"
          className={customStyles.main}
        >
          <div className={customStyles.bigSquareSignIn}>
            <p
              style={{
                fontSize: 25,
                fontFamily: "Lobster",
                marginTop: "5%",
                marginLeft: "5%",
                color: "#0278ae",
              }}
            >
              Find families and individuals best suited for your services.
            </p>
            <img
              src={imageSignIn}
              alt=""
              style={{
                marginTop: "-3%",
                height: "90%",
                width: "90%",
              }}
            />
          </div>

          <div className={customStyles.smallSquareSignIn}>
            <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
            >
              <h2 className={customStyles.welcomeMsg}>
                Hello,
                <br />
                Welcome Back
              </h2>

              <div
                style={{
                  width: "85%",
                  paddingTop: "10px",
                }}
              >
                <TextField
                  id="outlined-basic"
                  label="Username or Email"
                  variant="outlined"
                  size="small"
                  fullWidth="true"
                />
              </div>
              <div style={{ width: "85%", paddingTop: "10px" }}>
                <TextField
                  id="outlined-basic"
                  label="Username or Email"
                  variant="outlined"
                  size="small"
                  fullWidth="true"
                />
              </div>
              <div
                style={{
                  height: 20,
                  width: "85%",
                  paddingTop: 10,
                }}
              >
                <Grid
                  container
                  direction="row"
                  justify="space-between"
                  alignItems="center"
                  justifyContent="center"
                >
                  <div>
                    <FormControlLabel
                      style={{ height: 20, marginBottom: 0, marginTop: 0 }}
                      control={<Checkbox value="remember" color="primary" />}
                      label={
                        <span
                          style={{
                            color: "#272343",
                            fontSize: 13,
                            fontWeight: "bold",
                          }}
                        >
                          Remember me
                        </span>
                      }
                    />
                  </div>

                  <div style={{ height: "100%" }}>
                    <Link1
                      href="#"
                      variant="body2"
                      style={{
                        color: "#272343",
                        fontSize: 12,
                        fontWeight: "bold",
                      }}
                    >
                      Forgot password?
                    </Link1>
                  </div>
                </Grid>
              </div>

              <Link1 to="/PreDashboard" style={commonStyles.link}>
                {" "}
                <Button
                  className="loginButton"
                  style={{
                    textAlign: "center",
                    fontWeight: "bold",
                    paddingTop: 10,
                    height: 40,
                    color: "#FFF",
                    width: "85%",
                    marginTop: 25,
                    backgroundColor: "#FF7300",
                  }}
                >
                  Login
                </Button>
              </Link1>

              <div className={customStyles.orLoginWith}>or login with</div>

              <div className={customStyles.socialLine}>
                <Button
                  justIcon
                  href="#pablo"
                  target="_blank"
                  color="transparent"
                  onClick={(e) => e.preventDefault()}
                >
                  <i className={customStyles.socialIcons + " fab fa-twitter"} />
                </Button>
                <Button
                  justIcon
                  href="#pablo"
                  target="_blank"
                  color="transparent"
                  onClick={(e) => e.preventDefault()}
                >
                  <i
                    className={customStyles.socialIcons + " fab fa-facebook"}
                  />
                </Button>
                <Button
                  justIcon
                  href="#pablo"
                  target="_blank"
                  color="transparent"
                  onClick={(e) => e.preventDefault()}
                >
                  <i
                    className={
                      customStyles.socialIcons + " fab fa-google-plus-g"
                    }
                  />
                </Button>
              </div>

              <div className={customStyles.dontHaveAnAccount}>
                <span>Don't Have An Account?</span>
                <span className={customStyles.createOne}>
                  <Link1 to="/CreateAccount" style={commonStyles.linkNormal}>
                    {" "}
                    Create One{" "}
                  </Link1>
                </span>
              </div>
            </Grid>
          </div>
        </Grid>
      </div>
    </div>
  );
}
