// @ts-nocheck
import React from "react";
import { Grid, Divider } from "@material-ui/core";
import CustomizedSwitches from "Components/Settings/customizedSwitches";
import BillingTextInput from "Components/Settings/billingTextInput";
import SettingsCardNumber from "Components/Settings/settingsCardNumber";

import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function SettingTwoFactorAuth() {
  return (
    <Grid
      style={{
        maxWidth: 800,
        minWidth: 600,
        display: "flex",
        justifyContent: "center",
        marginTop: 15,
      }}
    >
      <Grid style={{ marginLeft: 340, marginBottom: 60 }} justify="flex-start">
        <Grid xs={12} sm={12} style={styles.settingPagesSubheading}>
          Two-factor authentication
          <div
            style={{
              fontSize: 14,
              fontWeight: 400,
              maxWidth: 800,
              minWidth: 600,
              marginTop: 10,
            }}
          >
            Each time you login in, you'll receive a unique, single-use code via
            text or through an authendicator app - your choice.
          </div>
          <div style={{ marginLeft: 50 }}>
            <CustomizedSwitches label="Send code via SMS" />
            <div
              style={{
                marginLeft: 50,
                fontSize: 14,
                fontWeight: 500,
                marginTop: -10,
              }}
            >
              Standard messaging rates apply
            </div>
          </div>
          <div style={{ marginLeft: 50 }}>
            <CustomizedSwitches label="Send code via Email" />
          </div>
        </Grid>
      </Grid>

      <Grid
        style={{
          maxWidth: 350,
          minWidth: 300,
        }}
      ></Grid>
    </Grid>
  );
}
