import React from "react";
import { Grid, Box } from "@material-ui/core";
import { Link as Link1 } from "react-router-dom";
import commonStyles from "Assets/Styles/loginSignup/commonStyles";

export default function BrowseOffers(props) {
  return (
    <Grid item>
      <Link1 to={props.link} style={commonStyles.linkBlack}>
        <Box
          className="BrowseBox"
          boxShadow={2}
          style={{
            height: 250,
            width: 400,
            backgroundColor: "#FFF",
            borderRadius: 30,
          }}
        >
          <div
            fullWidth="true"
            style={{
              display: "flex",
              // justifyContent: "flex-end",
              height: 100,
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <img
              src={props.img}
              alt=""
              style={{
                float: "right",
                marginTop: "5%",
                height: "auto",
                width: "auto",
                maxHeight: 100,
              }}
            />
          </div>

          <h4 style={{ textAlign: "center", paddingTop: 20 }}>{props.title}</h4>
          <p
            style={{
              fontWeight: 200,
              paddingLeft: 25,
              paddingTop: 5,
              paddingRight: 25,
              fontSize: 20,
            }}
          >
            {props.description}
          </p>
        </Box>
      </Link1>
    </Grid>
  );
}
