// @ts-nocheck
import { Divider, Grid } from "@material-ui/core";
import React from "react";

import { Button } from "react-bootstrap";
import SettingsBillingAddress from "Components/Settings/settingsBillingAddress";
import SettingsCreditCardInfo from "Components/Settings/settingsCreditCardInfo";
import SettingsYourSavedCards from "Components/Settings/settingsYourSavedCards";

export default function SettingsPaymentsMain() {
  function Spacer(props) {
    return (
      <Grid
        xs={12}
        sm={10}
        style={{
          marginTop: props.top,
          display: "flex",
          justifyContent: "center",
        }}
      >
        <Grid xs={12} sm={10}>
          <Divider />
        </Grid>
      </Grid>
    );
  }

  return (
    <Grid
      container
      spacing={2}
      xs={12}
      sm={8}
      justify="center"
      style={{
        marginLeft: 10,
        marginRight: 50,
        // marginTop: 50,
        height: "auto",
        backgroundColor: "#FFF",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: 15,
      }}
    >
      <Grid
        container
        justify="flex-start"
        style={{
          fontSize: 30,
          fontWeight: 700,
          marginTop: 50,
          marginLeft: 50,
        }}
      >
        Payment settings
      </Grid>

      <Grid justify="center">
        <SettingsYourSavedCards />
        <div style={{ height: 35 }} />
        {/* <Spacer top={35} /> */}
        <SettingsCreditCardInfo />
        <Spacer top={40} />
        <SettingsBillingAddress />
        {/* <Spacer top={35} /> */}
      </Grid>

      <Grid
        container
        justify="flex-end"
        spacing={2}
        xs={12}
        sm={8}
        style={{
          marginTop: 30,
          marginBottom: 50,
          marginLeft: 15,
        }}
      >
        <Button
          /* variant="success" */ style={{
            backgroundColor: "#28C397",
            width: 190,
            height: 50,
            border: "none",
          }}
        >
          Save changes
        </Button>
      </Grid>
    </Grid>
  );
}
