// @ts-nocheck
import React from "react";
import { Button, ButtonBase, ButtonGroup, Grid } from "@material-ui/core/";
import { makeStyles } from "@material-ui/core/styles";
import logo from "../../../Assets//Images/logo.png";
import Divider from "@material-ui/core/Divider";
import DashboardIcon from "@material-ui/icons/Dashboard";
import TrendingUpIcon from "@material-ui/icons/TrendingUp";
import DescriptionIcon from "@material-ui/icons/Description";
import PostAddIcon from "@material-ui/icons/PostAdd";
import MonetizationOnIcon from "@material-ui/icons/MonetizationOn";
import SidebarPageList from "./sidebarPageList";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    "& > *": {
      margin: theme.spacing(1),
    },
  },
}));

export default function GroupOrientation() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,

  });


  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };
  return (
    <div className={classes.root} style={{ height: "100%", backgroundColor: "#294272", display: "flex", alignItems: "flex-start" }}>
      <Grid
        item
        xs={12}
        container
        direction="row"
        style={{ margin: "auto", marginTop: 0}}
      >
        <Grid item xs={12} style={{}}>

       
            <Grid item xs={12} style={{ display: "flex", justifyContent: "center", borderRadius: "0px 0px 40px 0px", backgroundColor: "#ecf4f3", height: 110, alignItems:"center" }}>

              <img
                src={logo}
                alt="logo"
                style={{ maxWidth: 45, maxHeight: 25 }}
                onClick={toggleDrawer}
              />
              <div style={{ fontFamily: "Akaya Telivigala", fontSize: 20 }}>
                {" "}
                Mtempz
              </div>
            </Grid>
        


          <br /> <br />
        </Grid>
        {/* <Divider orientation="horizontal" /> */}
        <Grid container xs={12} direction="row" style={{marginLeft: 25}}>
          <SidebarPageList label="Dashboard" id={1} />
          <SidebarPageList label="User Activity" id={2} />
          <SidebarPageList label="Requests" id={3} />
          <SidebarPageList label="Proposals" id={4} />
          <SidebarPageList label="Earnings" id={5} />
          <SidebarPageList label="Past Projects" id={6} />


        </Grid>
      </Grid>
    </div>
  );
}
