import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Grid, Button } from "@material-ui/core";
import imageSignIn from "Assets/Images/loginSignupPage/imgSignupThird.svg";
import styles from "Assets/Styles/loginSignup/loginSignupStyles.js";
import "Assets/Styles/loginSignup/loginSignupStyles.css";
import { Link as Link1 } from "react-router-dom";
import commonStyles from "Assets/Styles/loginSignup/commonStyles";
import Navbar from "View/Appbar/navbar";
const useStyles = makeStyles(styles);

export default function SignupThird() {
  const customStyles = useStyles();
  return (
    <div>
      <Navbar />
      <div className={customStyles.root} style={{ background: "#EEECF0" }}>
        <br /> <br />
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="center"
          style={{
            height: 650,
            width: 500,
            backgroundColor: "#FFF",
            boxShadow: "0 0px 2px 0px #9E9E9E",
          }}
        >
          <Grid container direction="column" alignItems="center">
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <h2
                style={{
                  color: "#272343",
                  fontWeight: "bold",
                  fontSize: 30,
                  fontFamily: "Kanit",
                  marginBottom: "5%",
                }}
              >
                Account successfully created
              </h2>
            </div>

            <img
              src={imageSignIn}
              alt=""
              style={{
                height: "100%",
                width: "100%",
              }}
            />
            <Link1 to="/" style={commonStyles.link}>
              {" "}
              <Button
                className="loginButton"
                style={{
                  textAlign: "center",
                  fontWeight: "bold",
                  height: 40,
                  color: "#FFF",
                  width: "35%",
                  marginTop: 10,
                  backgroundColor: "#FF7300",
                }}
              >
                Let's Go!
              </Button>
            </Link1>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
