import React from "react";
import { Grid, TextField } from "@material-ui/core";

export default class ProjectTextInput extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      row: 1,
      title: "PROJECT TITLE",
      placeholder: "I need house keeper",
      desc: "Try to summarise your project in one sentence",
    };
  }

  componentDidMount() {
    this.initialValues();
  }

  initialValues() {
    if (this.props.isMultiline) {
      this.setState({
        row: 5,
        title: "PROJECT DESCRIPTION",
        placeholder:
          "Be descriptive, projects with good descriptions are more popular with our freelancers",
        desc: "I need a house keeper who have lot of experiences",
      });
    }
  }

  render() {
    return (
      <Grid
        xs={12}
        sm={10}
        style={{
          fontSize: 11,
          margin: "auto",
          marginLeft: 5,
          marginRight: 5,
          maxWidth: 1000,
        }}
      >
        <div
          style={{
            fontWeight: 900,
            color: "#000",
            fontSize: 12,
            marginBottom: 3,
          }}
        >
          {this.state.title}
        </div>

        <TextField
          hiddenLabel={true}
          id="outlined-basic"
          //   label="Display Name"
          placeholder={this.state.placeholder}
          variant="outlined"
          multiline={this.props.isMultiline}
          size="medium"
          fullWidth
          rows={this.state.row}
          style={{ borderColor: "#A4A4A4" }}
        />

        <div
          style={{
            fontWeight: 600,
            color: "#9D9D9D",
            marginTop: 5,
          }}
        >
          {this.state.desc}
        </div>
      </Grid>
    );
  }
}
