import React from "react";
import Chart from 'react-apexcharts'
import { Grid, Container, Typography, Paper } from "@material-ui/core";

export default class DoughnutChart extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      fill: {
        colors: ['#F44336', '#E91E63', '#9C27B0']
      },
      series: [44, 55, 41, 17, 26],
      options: {
        labels: ["Child Care", "Senior Care", "Special Needs", "Pet Care", "House Keeping"],
        chart: {
          type: 'donut',
        },
        plotOptions: {
          pie: {
            donut: {
              size: '65%'
            },
            customScale: 1.15,
            expandOnClick: true
          }
        },
        dataLabels: {
          enabled: false,
        },
        colors: ["#ac0d0d",'#eb5e0b', '#d89216','#ffd880','#fbe6c2'],
        legend: {
          show: true,
          fontWeight: 400,
          position: 'right',
          height: 600,
          offsetY: -23,
        }
      },
      

    };
  }



  render() {
    return (
      <Grid xs={12} item style={{display:'flex', justifyContent:'center', paddingTop: 30, paddingBottom: 30}}>
        <Chart options={this.state.options} series={this.state.series} type="donut" />
      </Grid>
    )
  }
}