// @ts-nocheck
import React from "react";

import {
  // TextField,
  Grid,
  // Button,
  // Box,
  // InputBase,
  // Divider,
} from "@material-ui/core";

import ReviewRatings from "Components/ViewOffer/reviewRatings";

import { Container, Row /* Button, Col */ } from "react-bootstrap";

export default function Review(props) {
  function Reply() {
    if (props.reply === true) {
      return (
        <Grid
          style={{
            marginTop: 20,
            height: 50,
            width: 500,
          }}
        >
          <Grid
            style={{
              marginTop: 20,
              paddingTop: 10,
              marginLeft: 50,
              height: 80,
              width: 485,
              backgroundColor: "#F8F9FC",
              borderStyle: "solid",
              borderWidth: 1,
              borderColor: "#DFE0E3",
            }}
          >
            <Row>
              <img
                src="https://poetsandquants.com/wp-content/uploads/sites/5/2019/05/beautiful-blur-blurred-background-733872.jpg"
                alt="profile pic"
                style={{
                  width: 40,
                  height: 40,
                  borderRadius: "50%",
                  marginTop: 10,
                  marginLeft: 40,
                }}
              />
              <Grid style={{ marginLeft: 20, marginTop: 5 }}>
                <Grid>Amiliya F.</Grid>

                <Grid
                  style={{
                    fontWeight: 300,
                    fontFamily: "Roboto",
                  }}
                >
                  {props.replyMsg}
                </Grid>
              </Grid>
            </Row>
          </Grid>
        </Grid>
      );
    } else {
      return <Grid></Grid>;
    }
  }

  return (
    <Container>
      <Row
        style={{
          minHeight: 30,
          marginLeft: 30,
        }}
      >
        <Grid
          style={{
            marginTop: 25,
          }}
        >
          <div style={{ fontSize: 12 }}>SUN 2.49 AM</div>
          <img
            src={props.senderImage}
            alt="sender"
            style={{
              width: 40,
              height: 40,
              borderRadius: "50%",
              marginTop: 10,
              marginLeft: "auto",
              display: "flex",
              justifyContent: "flex-end",
            }}
          />
        </Grid>

        <Grid
          style={{
            width: 560,
            height: "auto",
            marginLeft: 30,
            marginTop: 50,
            paddingBottom: 20,
            borderStyle: "solid",
            borderWidth: 1,
            borderColor: "#DFE0E3",
          }}
        >
          <Row
            style={{
              display: "flex",
              justifyContent: "space-between",
              marginRight: 20,
              alignItems: "center",
            }}
          >
            <Row
              style={{
                display: "flex",
                alignItems: "center",
                marginTop: 10,
                marginLeft: 30,
              }}
            >
              <Grid
                style={{
                  fontWidth: 600,
                  fontFamily: "Roboto",
                  fontSize: 16,
                  marginRight: 30,
                }}
              >
                {props.senderName}
              </Grid>
              <Row
                style={{
                  marginRight: "auto",
                  display: "flex",
                  justifyContent: "flex-start",
                }}
              >
                <i
                  className="fas fa-map-marker-alt"
                  style={{ marginRight: 5, marginTop: 3, fontSize: 12 }}
                ></i>

                <Grid
                  style={{ fontSize: 12, fontFamily: "Roboto", fontWidth: 500 }}
                >
                  {props.senderLocation}
                </Grid>
              </Row>
            </Row>

            <Grid style={{ marginTop: 10 }}>
              <ReviewRatings ratings={props.ratings} />
            </Grid>
          </Row>
          <Grid
            style={{ fontWeight: 300, fontFamily: "Roboto", marginLeft: 15 }}
          >
            {props.senderMsg}
          </Grid>

          <Reply />
        </Grid>
      </Row>
    </Container>
  );
}
