import React from "react";
import {Grid,} from "@material-ui/core";
import { Button } from "react-bootstrap";
import profileImage from "Assets/Images/Settings/settingsProfileImage.jpg";

export default function SettingsAvatar() {
  return (
    <Grid
      container
      spacing={2}
      justify="center"
      style={{ marginTop: 20, marginBottom: 10 }}
    >
      <div
        style={{
          marginLeft: 30,
          width: 140,
          height: 90,
          alignItems: "center",
        }}
      >
        <div
          style={{
            fontWeight: 700,
            fontSize: 14,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            marginBottom: 5,
          }}
        >
          {"change Avatar"}
        </div>
        <div
          style={{
            width: 110,
            height: 110,
            backgroundColor: "#EEE",
            borderRadius: "50%",
            fontWeight: 600,
            fontSize: 13,
            marginLeft: "auto",
            marginRight: "auto",
          }}
        >
          <img
            src={profileImage}
            alt ="profile"
            style={{ width: 110, height: 110, borderRadius: "50%" }}
          />
        </div>
      </div>

      <Button
        variant="outline-success"
        style={{
      
          borderRadius: 5,
      
          alignItems: "center",
          display: "flex",
          justifyContent: "center",
        }}
      >
        upload
      </Button>
    </Grid>
  );
}
