// @ts-nocheck
import React from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar';
import { Grid, Typography } from "@material-ui/core";

import ReviewCard from "./reviewCard";
import ReviewRatings from "../../ViewOffer/reviewRatings"

export default function ReviewBox() {
    return (
        <Grid item xs={12} style={{ alignItems: "center", marginTop: 15 }}>



            

            <Grid xs={12} item style={{ display: "flex", justifyContent: "center", alignItems: "center",marginBottom: 10, marginTop: 10 }}>
                <Typography style={{ fontFamily: "RocknRoll One", fontSize: 18, }}>
                    Customer reviews
            </Typography>
            </Grid>

           




            <Grid item xs={12} style={{ display: "flex", justifyContent: "center", }}>

                <Grid item xs={5} style={{ display: "flex", justifyContent: "center", alignItems: "center", height: 35, backgroundColor: "#F5F8FF", borderRadius: 10, paddingBottom: 6 }}>

                    <Grid item xs={12} style={{ display: "flex", justifyContent: "center", alignItems: "center", height: 25, backgroundColor: "#F5F8FF" }}>
                        <ReviewRatings ratings={4.7} style={{ display: "flex", justifyContent: "center", display: "flex", alignItems: "center" }} />

                        <Typography style={{ fontSize: 11, fontFamily: "RocknRoll One", display: "flex", alignItems: "center", marginLeft: 10, marginTop: 8 }}>4.7 out of 5</Typography>
                    </Grid>

                </Grid>
            </Grid>





            <Grid item xs={12} style={{ display: "flex", justifyContent: "center", marginTop: 6, marginBottom: 20 }}>
                <Typography style={{ fontSize: 11, fontFamily: "RocknRoll One" }}>380 customer ratings</Typography>
            </Grid>



            <ReviewCard labelText="Excellent" percentage={84} />
            <ReviewCard labelText="Good" percentage={9} />
            <ReviewCard labelText="Average" percentage={4} />
            <ReviewCard labelText="Blow Average" percentage={2} />
            <ReviewCard labelText="Poor" percentage={1} />
            <Grid style={{ height: 20 }} />

        </Grid>
    );
}