import React from "react";
// import { Image } from "react-bootstrap";
// import logo from "Assets/Images/logo.png";

import {
  Grid,
  Divider,
  //   Avatar,
  //   Menu,
  //   MenuItem,
  Typography,
} from "@material-ui/core";

// import styles from "Assets/Styles/Settings/settingsStyles.js";
import "Assets/Styles/Settings/settingsStyles.css";

// import "View/Appbar/navbar.css";

// import { /* Navbar,  */ Nav } from "react-bootstrap";
// import { DriveEtaOutlined } from "@material-ui/icons";

// import SettingsSideBar from "Components/Settings/settingsSideBar";

export default function NotificationCard(props) {
  var titleColor = props.isActive ? "black" : "grey";
  var timeColor = props.isActive ? "black" : "grey";
  var iconColor = props.isActive ? "black" : "#dddddd";

  function sayHello() {
    alert("Hello!");
  }
  function sayHello2() {
    alert("Hello2!");
  }

  return (
    <Grid>
      <Grid style={{ paddingLeft: 20, paddingRight: 20 }}>
        <Divider />
      </Grid>

      <Grid
        container
        xs={12}
        spacing={2}
        style={{
          height: 120,
          paddingTop: 15,
          paddingBottom: 15,
          paddingLeft: 25,
          // paddingRight: 20,
          display: "flex",
          alignItems: "center",
        }}
      >
        <Grid
          item
          xs={1}
          style={{
            display: "flex",
            justifyContent: "center",
            cursor: "pointer",
          }}
          onClick={sayHello}
        >
          <i
            className="far fa-envelope"
            style={{ fontSize: 23, color: iconColor }}
          />
        </Grid>

        <Grid item style={{ height: 70 }}>
          <Divider orientation="vertical" />
        </Grid>

        <Grid item xs={9} style={{ height: 70, marginTop: -5 }}>
          <Typography
            style={{
              fontSize: 13,
              fontWeight: 500,
              fontFamily: "RocknRoll One",
              color: titleColor,
              cursor: "pointer",
            }}
            onClick={sayHello}
          >
            {props.title}
          </Typography>

          <Typography
            style={{
              fontSize: 12,
              paddingTop: 3,
              fontWeight: 800,
              color: "#E191B9",
              fontFamily: "Roboto",
            }}
          >
            {props.type}
          </Typography>

          <Typography
            style={{
              fontSize: 12,
              fontFamily: "Kanit",
              color: timeColor,
            }}
          >
            {props.time}
          </Typography>
        </Grid>
        <Grid
          item
          xs={1}
          style={{
            display: "flex",
            justifyContent: "center",
          }}
        >
          <div
            style={{
              fontSize: 12,
              fontFamily: "Kanit",
              fontWeight: 800,
              cursor: "pointer",
            }}
            onClick={sayHello2}
          >
            X
          </div>
          {/* <i className="fas fa-times" style={{ fontSize: 12, color: "grey" }} /> */}
        </Grid>
      </Grid>
    </Grid>
  );
}
