// @ts-nocheck
import React from "react";
import { Grid, Divider } from "@material-ui/core";

import { Button } from "react-bootstrap";

import SettingsAvatar from "Components/Settings/settingsAvatar";
import SettingsTextField from "Components/Settings/settingsTextField";
import SettingsLinkedAccounts from "Components/Settings/settingsLinkedAccounts";
import SettingsDeleteAccount from "Components/Settings/settingsDeleteAccount";
import CustomizedSwitches from "Components/Settings/customizedSwitches";
import SettingsAlerts from "Components/Settings/settingsAlerts";
import CategoriesOfInterest from "Components/Settings/categoriesOfInterest";
import InterestCategory from "Components/Settings/interestCategory";
import SettingsBillingAddress from "Components/Settings/settingsBillingAddress";
import SettingsCreditCardInfo from "Components/Settings/settingsCreditCardInfo";
import SettingsYourSavedCards from "Components/Settings/settingsYourSavedCards";

import SettingsChangePassword from "Components/Settings/settingsChangePassword";
import SettingTwoFactorAuth from "Components/Settings/settingTwoFactorAuth";

import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function SettingsPrivacyMain() {
  function Spacer(props) {
    return (
      <Divider
        style={{
          maxWidth: 800,
          minWidth: 600,
          marginTop: props.top,
        }}
      />
    );
  }

  return (
    <Grid
      container
      spacing={2}
      justify="center"
      style={{
        marginLeft: 10,
        marginRight: 10,
        marginTop: -50,
        maxWidth: 800,
        minWidth: 600,
        height: "auto",
        backgroundColor: "#FFF",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: 15,
      }}
    >
      <Grid
        container
        justify="flex-start"
        style={{
          fontSize: 30,
          fontWeight: 700,
          marginTop: 50,
          marginLeft: 50,
        }}
      >
        Privacy settings
      </Grid>

      <SettingsChangePassword />
      <Spacer top={35} />
      <SettingTwoFactorAuth />
    </Grid>
  );
}
