// @ts-nocheck
import React from "react";
import { Grid, Divider } from "@material-ui/core";
import CustomizedSwitches from "Components/Settings/customizedSwitches";
import BillingTextInput from "Components/Settings/billingTextInput";
import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function SettingsYourSavedCards() {
  return (
    <Grid xs={12} sm={10} style={styles.settingPagesSubheading}>
      <div style={{ marginTop: 30 }}> Your saved cards </div>
      <Grid
        container
        style={{ marginTop: 20, display: "center", justifyContent: "center" }}
      >
        <div
          style={{
            height: 150,
            width: 240,
            backgroundColor: "#FFF",
            border: "1px solid #000",
            borderRadius: 15,
            margin: 10,
          }}
        >
          <div style={{ marginLeft: 15, marginRight: 15 }}>
            <Grid
              container
              direction="row"
              justify="space-between"
              alignItems="center"
            >
              <img
                src="https://upload.wikimedia.org/wikipedia/commons/7/72/MasterCard_early_1990s_logo.png"
                style={{ height: 20, width: 30, marginTop: 20 }}
              />

              <div style={{ color: "#cc561e" }}>Edit</div>
            </Grid>

            <div style={{ marginTop: 15, fontSize: 9, color: "#C8C8C8" }}>
              CARD RUNNER
            </div>
            <div style={{ marginTop: 3, fontSize: 13 }}>
              **** **** **** 2345
            </div>

            <Grid
              container
              direction="row"
              justify="space-between"
              alignItems="center"
            >
              <div>
                <div style={{ marginTop: 15, fontSize: 9, color: "#C8C8C8" }}>
                  CARD HOLDER
                </div>
                <div style={{ marginTop: 3, fontSize: 13 }}>
                  Charlotte Reeves
                </div>
              </div>

              <div>
                <div
                  style={{
                    marginTop: 15,
                    fontSize: 9,
                    color: "#C8C8C8",
                    display: "flex",
                    justifyContent: "flex-end",
                  }}
                >
                  EXPIRY DATE
                </div>
                <div style={{ marginTop: 3, fontSize: 13 }}>04/27</div>
              </div>
            </Grid>
          </div>
        </div>

        <div
          style={{
            height: 150,
            width: 240,
            backgroundColor: "#FFF",
            border: "1px solid #000",
            borderRadius: 15,
            margin: 10,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <div>{"+ Add new card"}</div>
        </div>
      </Grid>
    </Grid>
  );
}
