import React from "react";
import {Grid,Typography} from "@material-ui/core";
import "Assets/Styles/Settings/settingsStyles.css";

import SettingsNotificationBar from "Components/Settings/settingsNotificationBar";
import SettingsSideBar from "Components/Settings/settingsSideBar";
import styles from "Assets/Styles/Settings/settingsStyles.js";

import { Row  } from "react-bootstrap";
import Navbar from "View/Appbar/navbar";

export default class SettingsNotification extends React.Component {

  render() {
    return (
      <div>
        <Navbar />
      
       < Grid item container direction="row">
          <Grid item xs={12} sm={10} style={{margin:"auto",fontWeight: 700,backgroundColor:"blue"}}align="center">
          <SettingsNotificationBar />
          </Grid>
          <Grid item xs={12} sm={2}>
          <Typography  style={{fontSize: 35,fontWeight: 700}} align="center">
                {"Settings "}
              </Typography>
             <SettingsSideBar notification />
          </Grid>

        </Grid>
      </div>
    );
  }
}
