import { DropdownButton, ButtonGroup } from "react-bootstrap"
import Select from "react-select";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";

const options = [
  { value: "Child Care", label: "Child Care",topic:"All Services" },
  { value: "Senior Care", label: "Senior Care" },
  { value: "Skin Care", label: "Skin Care" },
  { value: "Toturing", label: "Toturing" },
  { value: "House Keeping", label: "House Keeping" },
  { value: "Transport Services", label: "Transport Services" },
];
const useStyles = makeStyles((theme) => ({
  root: {
    padding: "2px 4px",
    display: "flex",
    alignItems: "center",
    width: "auto",
    minWidth: 400,
    maxWidth: 1165,
    height: 60,
    margin: "auto",
    border: 10,
    borderBlockColor: "black",
    color: "white",
    boxShadow: 3,
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
  },
  divider: {
    height: 28,
      margin: "auto",
      width:1,
  },
}));
const customStyles = {
  option: (provided, state) => ({
    ...provided,
    // borderBottom: "1px dotted pink",
    color: state.isSelected ? "whtie" : "black",
    // padding: 20,
  }),
  control: () => ({
    // none of react-select's styles are passed to <Control />
    width: 250,
  }),
  singleValue: (provided, state) => {
    const opacity = state.isDisabled ? 0.5 : 1;
    const transition = "opacity 300ms";

    return { ...provided, opacity, transition };
  },
  input: () => ({
    fontSize: 20,
    fontWeight: "bold",
  }),
};

function Filter() {
    const classes = useStyles();
    return (
      <div align="center">
        <Paper component="form" className={classes.root}>
          {[
            "Services",
            "Location",
            "Language",
            "Popularity",
            "Cert",
            "Rate",
          ].map((variant) => (
            <div >
              <DropdownButton
                as={ButtonGroup}
                key={variant}
                id={`dropdown-variants-${variant}`}
                variant="Primary"
                      title={variant}
                      
              >
                
                <div>
                  {" "}
                  <Select
                    components={{
                      DropdownIndicator: () => null,
                      IndicatorSeparator: () => null,
                    }}
                    isSearchable={true}
                    placeholder="All Sevices"
                    styles={customStyles}
                    options={options}
                  />
                </div>
              </DropdownButton>
            </div>
          ))}
        </Paper>
      </div>
    );
} export default Filter;