// @ts-nocheck
import React from "react";
import { ButtonBase, Grid } from "@material-ui/core/";
import DashboardIcon from "@material-ui/icons/Dashboard";
import { useSelector, useDispatch } from "react-redux";
import { changePage, changePageName } from "States/DashboardNavigation";

export default function SidebarPageList(props) {
  const pageNumber = useSelector((state) => state.pageNumber);
  const dispatch = useDispatch();

  var id = props.id;

  var isActive = pageNumber == id;

  var activeColor = pageNumber == id ? "#0749b3" : "#FFF";

  var iconName =
    id == 1
      ? "fas fa-th"
      : id == 2
      ? "fas fa-chart-line"
      : id == 3
      ? "fas fa-paste"
      : id == 4
      ? "far fa-newspaper"
      : id == 5
      ?"fas fa-dollar-sign"
      :"fas fa-paste";

  return (
    <div
      onClick={() => {
        dispatch(changePage(id));
        dispatch(changePageName(props.label));
      }}
      style={{ cursor: "pointer", marginTop: 20, marginBottom: 20 }}
    >
      <Grid
        item
        xs={12}
        style={{
          height: 45,
          width: 170,
          backgroundColor: isActive ? "#FFF" : "#294272"  ,
          display: "flex",
          alignItems: "center",
          borderRadius: 15,
          paddingLeft: 15
        }}
      >
        <div style={{ width: 35, display: "flex", justifyContent: "center" }}>
          <i
            className={iconName}
            style={{ fontSize: 22, color: activeColor }}
          ></i>
        </div>

        <div
          style={{
            paddingLeft: 8,
            paddingRight: 8,
            color: activeColor,
          }}
        >
          {props.label}
        </div>
      </Grid>
    </div>
  );
}
