import React from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import { } from "@material-ui/core/";
import { AppBar, Button, Grid } from "@material-ui/core/";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import Badge from "@material-ui/core/Badge";
import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";
import Link from "@material-ui/core/Link";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import NotificationsIcon from "@material-ui/icons/Notifications";
import DashboardIcon from "@material-ui/icons/Dashboard";
import ContactMailIcon from "@material-ui/icons/ContactMail";
import SpeakerNotesIcon from "@material-ui/icons/SpeakerNotes";
import { Height, ViewArray } from "@material-ui/icons";
import SideTab from "../../Components/DashBoard/sideTab";
import Search from "../../View/FreeLancer/Search/search";

import DashboardNavbar from "../../Components/DashBoard/dashboardNavbar";

import SideBar from "../../Components/DashBoard/Sidebar/sidebar";
import Req from "../../Components/DashBoard/RequestPage/CardView";
import Proposals from "../../Components/DashBoard/ProposalsPage/CardView";
import MainDashboard from "../../Components/DashBoard/MainDashboard/mainDashboard";
import Earning from "../../Components/DashBoard/EarningPage/earning";
import { useSelector, useDispatch } from "react-redux";
import { changePage, changePageName } from "States/DashboardNavigation";
import PastProjects from "../../Components/DashBoard/PastProjects/CardView"
import UserActivity from "../../Components/DashBoard/UserActivity/userActivity"

import { connect } from 'react-redux';
import {
  useParams
} from "react-router-dom";


class Dashboard extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    const pageName = this.props.match.params.page;
    // const pageNumber = this.props.location.state.page;

    var page = "Dashboard";
    var pageNumber = 1;


    if (pageName != null) {
      switch (pageName) {
        case "Dashboard":
          page = "Dashboard";
          pageNumber = 1;
          break; 
          
          case "UserActivity":
          page = "User Activity";
          pageNumber = 2;
          break; 
          
          case "Requests":
          page = "Requests";
          pageNumber = 3;
          break; 
          
          case "Proposals":
          page = "Proposals";
          pageNumber = 4;
          break; 
          
          case "Earnings":
          page = "Earnings";
          pageNumber = 5;
          break;

          case "PastProjects":
          page = "Past Projects";
          pageNumber = 6;
          break;

        default:
          page = "Dashboard";
          pageNumber = 1;
          break;
      }
    }

    this.props.changePage(pageNumber);
    this.props.changePageName(page);
  }


  render() {
    return (
      <div /* className={classes.root} */>
        <Grid
          item
          xs={12}
          sm={12}
          container
          direction="row"
          style={{ margin: "auto", minHeight: "auto" }}
        >
          <Grid item xs={2} /* sm={2} */ direction="column" style={{}}>


            <SideBar />


          </Grid>
          <Divider />

          <Grid
            item
            xs={10}
            // sm={10}
            direction="column"
            style={{ background: "" }}
          >
            <Grid item xs={12} sm={12} style={{ height: 60, background: "#FFF" }}>
              <DashboardNavbar />
            </Grid>

            <Grid
              item
              xs={12}
              sm={12}
              style={{ background: "white", minHeight: 800 }}
            >
              {/*   <Req /> */}
              <PageContent number={this.props.pageNumber} />
              <br />
            </Grid>
          </Grid>
        </Grid>
      </div>
    );
  }
}


const mapStateToProps = state => {
  return {
    pageNumber: state.pageNumber
  }
}

const mapDispathToProps = dispatch => {
  return {
    changePageNumber: () => dispatch(changePage(3))
  }
}

export default connect(mapStateToProps, { changePage, changePageName })(Dashboard)



function PageContent(props) {
  switch (props.number) {
    case 1:
      return <MainDashboard />;
    case 2:
      return <UserActivity />;
    case 3:
      return <Req />;
    case 4:
      return <Proposals />;
    case 5:
      return <Earning />;
    case 6:
      return <PastProjects/>;

    default:
      return <Req />;
  }
}