import { Carousel } from "react-bootstrap";
import { Container, Button } from "@material-ui/core";
import "./carousel.css";
import ci1 from "Assets/Images/Carousel/c3.jpeg";
import ci2 from "Assets/Images/Carousel/c2.jpeg";
export default function Carouselview() {
  return (
    <Carousel>
      <Carousel.Item interval={1000}>
        <img
          className="d-block w-100"
          src={ci1}
          alt="First slide"
          style={{
            maxWidth: 1460,
            minWidth: 300,
            Height: "auto",
            minHeight: 350,
            maxHeight: 670,
          }}
        />
        <Container align="justify">
          {" "}
          <Carousel.Caption className="carousel">
            <div className="carousel head" style={{ color: "black" }}>
              Post Your Job Here
              <br />
              <Button
                size="large"
                variant="contained"
                style={{ backgroundColor: "#f17855", color: "white" }}
              >
                Post Job
              </Button>
            </div>
          </Carousel.Caption>
        </Container>
      </Carousel.Item>
      <Carousel.Item interval={500}>
        <img
          className="d-block w-100"
          alt="Third slide"
          src="https://www.mtempz.com/assets/img/background-new.jpg"
          style={{
            minHeight: 350,
            maxHeight: 670,
            maxWidth: 1460,
            minWidth: 300,
          }}
        />
        <Carousel.Caption className="carousel">
          <div className=" carousel head">
            <div>
              Find EXCEPTIONAL
              <br />
              SERVICES here
            </div>
            <Button
              size="large"
              variant="outlined"
              style={{
                backgroundColor: "#f17855",
                color: "#fff",
                border: " #f17855",
              }}
            >
              Get Started
            </Button>{" "}
          </div>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item interval={500}>
        <img
          className="d-block w-100"
          src={ci2}
          alt="Third slide"
          style={{
            minHeight: 350,
            maxHeight: 670,
            maxWidth: 1460,
            minWidth: 300,
          }}
        />
        <Carousel.Caption className="carousel">
          <div className=" carousel head" style={{ color: "black" }}>
            Hire a Provider
            <br />
            <Button
              size="large"
              variant="outlined"
              style={{ backgroundColor: "#f17855", color: "white" }}
            >
              Hire
            </Button>{" "}
          </div>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}
