import React from "react";
import { Box, Divider } from "@material-ui/core";
import PropTypes from "prop-types";

export default class InterestCategory extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isSelected: this.props.isActive,
    };

    this.delta = this.delta.bind(this);
  }

  delta() {
    this.setState({
      isSelected: !this.state.isSelected,
    });
  }

  render() {
    var background = this.state.isSelected ? "#408BD3" : "#EFF2F3";
    var textColor = this.state.isSelected ? "#FFF" : "#000";

    return (
      <div
        style={{
          backgroundColor: background,
          color: textColor,
          width: "auto",
          height: 36,
          borderRadius: 5,
          margin: 10,
          paddingLeft: 15,
          paddingRight: 15,
          marginRight: 10,
          marginLeft: 10,
          fontWeight: 700,
          alignItems: "center",
          display: "flex",
          justifyContent: "center",
          fontSize: 14,
        }}
        onClick={this.delta}
      >
        {this.props.label}
      </div>
    );
  }
}

InterestCategory.propTypes = {
  label: PropTypes.string,
  isActive: PropTypes.bool,
};

InterestCategory.defaultProps = {
  isActive: false,
};
