// @ts-nocheck
import React from "react";
import { Grid, Divider,Paper } from "@material-ui/core";
import { Button } from "react-bootstrap";
import SettingsAvatar from "Components/Settings/settingsAvatar";
import SettingsTextField from "Components/Settings/settingsTextField";
import SettingsLinkedAccounts from "Components/Settings/settingsLinkedAccounts";
import SettingsDeleteAccount from "Components/Settings/settingsDeleteAccount";
import { GridFilterMenuItem } from "@material-ui/data-grid";

export default function SettingsMainBar() {
  function Spacer(props) {
    return (
      <Divider
        style={{ 
          maxWidth: 800,
          minWidth: 600,
          marginTop: props.top,
        }}
      />
    );
  }

  return (
    <Grid
     container
     justify="center"
    
      style={{
        backgroundColor: "#FFF",
        borderRadius: 15,
      }}
    >
      <Paper elevation="3" style={{
        backgroundColor: "#FFF",
        borderRadius: 15,
      }}>
      <Grid
        container
       
        style={{
          fontSize: 30,
          fontWeight: 700,
         marginLeft:20
        }}
      >
        Account settings
      </Grid>

      <SettingsAvatar />

      <br/> <br/> <br/>

      <Grid
        container
        spacing={0}
        justify="center"
      direction="column"
      >
       
          <Grid
            container direction="row"
            xs={12} sm={12} spacing={1}
            justify="center"
          > <Grid item   >
            <SettingsTextField
              title="Display name"
              desc="Visible to other members"
              placeholder="James"
            />
            </Grid>
            <Grid item   >
            <SettingsTextField
              title="Full name"
              desc="How do you want to be called?"
              placeholder="James McDowel"
            /></Grid>
          </Grid>

         <br/>

          <Grid
            container direction="row"
            xs={12} sm={12} spacing={1}
            justify="center"
          > <Grid item >
            <SettingsTextField
              title="Email address"
              desc="For notifications and logging in"
              placeholder="james@gmail.com"
            />
           </Grid>
           <Grid item >
            <SettingsTextField
              title="Phone number"
              desc="For receiving notifications"
              placeholder="+41 345 5634 56"
            /></Grid>
          </Grid>

          <Grid item>  <Divider orientation="horizontal" /></Grid>
          <SettingsLinkedAccounts />
          <Grid item>  <Divider orientation="horizontal" /></Grid>
          <SettingsDeleteAccount />
          <Grid item>  <Divider orientation="horizontal" /></Grid>

          <Grid
           item container
           direction="row"
           justify="space-around"
           alignItems="center"
           
         >
         <Grid item justify="space-around"
           alignItems="center" spacing={2}>  
           <Button variant="success">
             Save changes
           </Button>
          </Grid>
        <br/>
      </Grid>
      </Grid>
      </Paper>
    </Grid>
  );
}
