// @ts-nocheck
import React from "react";
import {Grid} from "@material-ui/core";
import PropTypes from "prop-types";
import commonStyles from "Assets/Styles/loginSignup/commonStyles";
import { Link as Link1 } from "react-router-dom";
import NavItems from "Components/Settings/navItems";

export default function SettingsSideBar(props) {
  return (
    <Grid>
      {/* <ButtonBase> */}
      <Link1 to="/Settings" style={commonStyles.linkNormal}>
        <NavItems title="Account" isActive={props.account} />
      </Link1>
      {/*  </ButtonBase>
      <ButtonBase> */}
      <Link1 to="/SettingsNotification" style={commonStyles.linkNormal}>
        <NavItems title="Notification" isActive={props.notification} />
      </Link1>
      {/*  </ButtonBase> */}

      <Link1 to="/SettingsPayments" style={commonStyles.linkNormal}>
        <NavItems title="Payments" isActive={props.payments} />
      </Link1 >

      <Link1 to="/SettingsPrivacy" style={commonStyles.linkNormal}>
        <NavItems title="Privacy" isActive={props.privacy} /> 
      </Link1 >

      
    </Grid>
  );
}
SettingsSideBar.propTypes = {
  account: PropTypes.bool,
  notification: PropTypes.bool,
  payments: PropTypes.bool,
  privacy: PropTypes.bool,
};

SettingsSideBar.defaultProps = {
  account: false,
  notification: false,
  payments: false,
  privacy: false,
};
