import React from "react";

import StarRatings from "react-star-ratings";

export default function ReviewRatings(props) {
  return (
    <StarRatings
      
      rating={props.ratings}
      starRatedColor="orange"
      //   changeRating={this.changeRating}
      numberOfStars={5}
      name="rating"
      starDimension="20px"
      starSpacing="2px"
    />
  );
}
