import React from "react";
import { Box } from "@material-ui/core";

export default function PopularOffers(props) {
  return (
    <Box
      style={{
        fontSize: 12,
        color: "#000",
        // backgroundColor: "#E8ECF2",
        backgroundColor: "#f1f1f1",
        margin: 5,
        marginLeft: 15,
        marginRight: 15,
        paddingLeft: 20,
        paddingRight: 20,
        paddingTop: 2,
        paddingBottom: 2,
        borderRadius: 7,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        fontFamily: "Roboto",
      }}
    >
      {props.text}
    </Box>
  );
}
