import React from "react";
import { Grid, Button, Box } from "@material-ui/core";
import { Link as Link1 } from "react-router-dom";
import commonStyles from "Assets/Styles/loginSignup/commonStyles";

export default function PostProject() {
  return (
    <Grid item>
      <Box
        boxShadow={2}
        style={{
          height: 120,
          width: 825,
          backgroundColor: "#FFF",
        }}
      >
        <Grid container direction="row" justify="center" alignItems="center">
          <div
            style={{
              marginLeft: 10,
            }}
          >
            <Grid
              container
              direction="column"
              spacing={3}
              fullWidth="true"
              style={{ width: 600 }}
            >
              <h4 style={{ paddingTop: 30 }}>{"POST PROJECT"}</h4>
              <p
                style={{
                  paddingTop: 5,
                  fontSize: 20,
                }}
              >
                "Post what you need, then receice proposals from freelancers"
              </p>
            </Grid>
          </div>

          <Grid
            container
            justify="center"
            alignItems="center"
            style={{
              width: "auto",
              height: 120,
              alignItems: "center",
              justifyContent: "Center",
            }}
          >
            <Link1 to="/PostProject" style={commonStyles.link}>
              <Button
                className="BtnPostProject"
                fullWidth="true"
                style={{
                  textAlign: "center",
                  fontWeight: "bold",
                  justify: "center",
                  alignItems: "center",
                  height: 50,
                  color: "#FFF",
                  width: "auto",
                  minWidth: 180,
                  backgroundColor: "#FF7300",
                }}
              >
                POST JOB
              </Button>
            </Link1>
          </Grid>
        </Grid>
      </Box>
    </Grid>
  );
}
