import React from "react";

import {
  // TextField,
  // Grid,
  Button,
  Box,
  Divider,
} from "@material-ui/core";

import { Container, Row, Button as ButtonHover } from "react-bootstrap";

export default function OrderSide() {
  return (
    <Container
      style={{
        maxHeight: 870,
        maxWidth: 300,
      }}
    >
      <Box
        boxShadow={2}
        style={{
          backgroundColor: "#f4f4f4",
          height: 430,
          width: 300,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          // paddingBottom: 30,
          marginLeft: 30,
          marginTop: 30,
        }}
      >
        <Box
          boxShadow={2}
          style={{
            backgroundColor: "#FFF",
            height: 360,
            width: 300,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <span
            style={{
              paddingTop: 50,
              fontSize: 35,
              fontFamily: "Roboto",
              fontWeight: 800,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            {" $15 / h "}
          </span>

          <Button
            style={{
              backgroundColor: "#FF7300",
              width: 200,
              height: 50,
              marginTop: 15,
              color: "#FFF",
              fontFamily: "Roboto",
              fontSize: 18,
              fontWeight: 700,
              alignItems: "center",
              borderWidth: 0,
            }}
          >
            {"Apply"}
          </Button>

          <Row
            style={{
              marginTop: 35,
              height: 20,
              width: 230,
              justifyContent: "space-between",
            }}
          >
            <Row
              style={{
                marginRight: 40,
              }}
            >
              <span
                style={{
                  fontFamily: "Roboto",
                  fontSize: 14,
                }}
              >
                {"VIEWS"}
              </span>
              <span
                style={{
                  fontFamily: "Roboto",
                  fontWeight: 500,
                  marginLeft: 5,
                  fontSize: 14,
                }}
              >
                {"3,056"}
              </span>
            </Row>

            <Row>
              <i
                className="far fa-heart"
                style={{ fontSize: 14, marginTop: 3 }}
              />
              <span
                style={{ marginLeft: 5, fontSize: 14, fontFamily: "Roboto" }}
              >
                {"200"}
              </span>
            </Row>
          </Row>

          <Divider style={{ width: 275, marginTop: 5 }} />

          <Row
            style={{
              marginTop: 30,
              justifyContent: "left",
              alignItems: "left",
              width: 280,
            }}
          >
            <img
              src={
                "https://poetsandquants.com/wp-content/uploads/sites/5/2019/05/beautiful-blur-blurred-background-733872.jpg"
              }
              alt = "profile pic"

              style={{
                width: 50,
                height: 50,
                borderRadius: "50%",
              }}
            />

            <div
              style={{
                fontSize: 14,
                width: 100,
                marginLeft: 10,
              }}
            >
              by Amiliya F.
              <div>
                <Row>
                  <i
                    className="fas fa-star"
                    style={{
                      color: "#f6830f",
                      fontSize: 14,
                      marginTop: 3,
                      marginLeft: 15,
                      paddingRight: 2,
                    }}
                  ></i>
                  <span style={{ color: "#f6830f", marginRight: 3 }}>4.9</span>(
                  3,456 )
                </Row>
              </div>
            </div>
          </Row>

          <Row
            style={{
              width: 270,
              justifyContent: "space-between",
              alignItems: "center",
              marginTop: 10,
              marginBottom: 20,
            }}
          >
            <Row style={{ marginLeft: 5 }}>
              <i
                className="fas fa-map-marker-alt"
                style={{ fontSize: 13, marginTop: 3, marginRight: 4 }}
              />
              <span style={{ fontSize: 15, fontFamily: "Roboto" }}>Alaska</span>
            </Row>

            <ButtonHover variant="outline-danger">Contact</ButtonHover>
          </Row>
        </Box>
      </Box>
    </Container>
  );
}
