// @ts-nocheck
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Divider from "@material-ui/core/Divider";
import { Grid} from "@material-ui/core";
import Navbar from "View/Appbar/navbar";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Selector from "Components/PostProject/selector";

import ProjectTextInput from "Components/PostProject/projectTextInput";
import ProjectDropZone from "Components/PostProject/projectDropZone";
import Currency from "Components/PostProject/currency";
import Slider from "Components/PostProject/slider";
import DatePicker from "Components/PostProject/datePicker";
const useStyles = makeStyles((theme) => ({
  root: {
    width: "auto",
    minWidth: 500,
    maxWidth: 1265,
    height: "auto",
    margin: "auto",
    border: 10,
    borderBlockColor: "black",
    color: "white",
    boxShadow: 3,
  },
  form: {
    margin: "auto",
    marginLeft: 20,
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
  },
  divider: {
    height: 1,
    margin: 4,
  },
  label: {
    color: "black",
    font: "roboto",
    margin: "auto",
    marginTop: 5,
  },
  image: {
    width: 128,
    height: 128,
  },
  img: {
    margin: "auto",
    display: "block",
    maxWidth: 100,
    maxHeight: "100%",
  },
  card: {
    maxWidth: 200,
    width: "auto",
    Boxshadow: 3,
  },
  paper: {
    width: "auto",
    Boxshadow: 3,
    margin: "auto",
  },
  paperc: {
    width: "auto",
    Boxshadow: 3,
    height: "auto",
    maxHeight: 460,
  },
  Typography: {
    marginLeft: 20,
  },
  Button: {
    margin: "auto",

    marginBottom: 20,
    align: "center",
  },
  grid: { margin: "auto" },
}));

function HireForm() {
  const classes = useStyles();
  return (
    <div>
      <Navbar />
      <br />
      {/* <Paper component="form" className={classes.root}> */}
      <br />

      <br />
      <Grid container spacing={1}>
        <Grid
          item
          xs={12}
          sm={10}
          container
          direction="row"
          style={{ margin: "auto" }}
        >
          <Grid item sm container direction="column" spacing={0}>
            <Paper
              component="form"
              className={classes.paper}
              style={{ align: "center" }}
            >
              <Grid item xs>
                <Typography
                  variant="h4"
                  color="textSecondary"
                  component="p"
                  color="black"
                  font="roboto"
                  className={classes.Typography}
                  style={{ marginTop: 10 }}
                >
                  Post Project
                </Typography>
                <Typography
                  variant="h5"
                  color="textSecondary"
                  component="p"
                  className={classes.Typography}
                  style={{ marginTop: 10 }}
                >
                  Describe what you need, then receive custom proposals from
                  freelancers.
                </Typography>
                <br />
                <br />
                <Typography
                  variant="body1"
                  color="textSecondary"
                  component="p"
                  className={classes.Typography}
                >
                  Select a relevant category so that freelancers can find your
                  project
                </Typography>
                <Selector />

                <Grid
                  container
                  style={{
                    maxWidth: 1000,
                    Boxshadow: 3,
                    margin: "auto",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  <ProjectTextInput isMultiline={false} />
                  <ProjectTextInput isMultiline={true} />

                  <ProjectDropZone />

                  <Grid
                    item
                    sm
                    container
                    direction="row"
                    style={{ margin: "auto" }}
                  >
                    {/* <Currency /> <Slider /> */}
                    <Grid item className={classes.grid}>
                      <Currency />
                    </Grid>{" "}
                    <Grid item className={classes.grid}>
                      <Slider />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid
                  item
                  sm
                  container
                  direction="row"
                  style={{ margin: "auto" }}
                >
                  {/* <Currency /> <Slider /> */}
                  <Grid item className={classes.grid}>
                    <DatePicker />
                  </Grid>{" "}
                  {/* <Grid item className={classes.grid}>
                    <Slider />
                  </Grid> */}
                </Grid>
                <br />
                <div className={classes.Button} align="center" hide>
                  <Button
                    size="large"
                    variant="contained"
                    color="primary"
                    style={{ backgroundColor: "#f17855" }}
                  >
                    POST PROJECT
                  </Button>
                </div>
                {/* <div className={classes.Button} align="center" hide>
                  <Button
                    size="large"
                    variant="contained"
                    color="primary"
                    style={{ backgroundColor: "#f17855" }}
                  >
                    Countinue
                  </Button>
                </div> */}
              </Grid>
            </Paper>
          </Grid>
        </Grid>
        <Grid
          item
          style={{
            marginRight: "auto",
            marginLeft: "auto",
          }}
        >
          {/* <Paper
          // component="form"
          // className={classes.paperc}
          // backgroundColor: "#FF0",
          > */}

          <Grid item xs={12} sm={11} container align="justify">
            <Card className={classes.card}>
              <CardActionArea>
                <div className={classes.label} align="center">
                  USEFUL TIPS
                </div>
                <CardContent>
                  <Divider
                    className={classes.divider}
                    orientation="horizontal"
                  />
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                  >
                    1.Describe your project in as much detail as you can
                    comfortably reveal - it will increase the quality of
                    proposals you receive and shorten the selection process.
                  </Typography>
                  <br />
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                  >
                    2.Upload as much relevant information (pictures, documents,
                    specifications, links, etc) as possible to get a realistic
                    quote.
                  </Typography>
                  <br />
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                  >
                    3.Match the experience level to your requirements –
                    remember, you’re looking for the best you can afford, not
                    the cheapest you can get.
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
          {/* </Paper> */}
        </Grid>
      </Grid>
      {/* </Paper> */}
      <br />
      <br />
    </div>
  );
}
export default HireForm;
