import React from "react";
import {
  Typography,
  Button,
  Card,
  CardContent,
  CardActions,
  Avatar,
  Divider,Grid
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import RequestViewPage from "./requestViewPage";

const CoffeCard = (props) => {
  const { title, name, payment, career, imageUrl, view, date } = props;
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      boxShadow:3,
      borderRadius:10
    },
    paper: {
      padding: theme.spacing(2),
      margin: "auto",
      maxWidth: 1600,
    },
    image: {
      width: 128,
      height: 128,
    },
    img: {
      margin: "auto",
      display: "block",
      maxWidth: "100%",
      maxHeight: "100%",
    },
    head: {    fontSize: 20 ,fontFamily: "Roboto",fontWeight:500,marginBottom:5 },
    name: {  fontSize: 15 ,margin:"auto"},
    day: {
      fontSize: 14,
      backgroundColor: "#e1f2fc",
      borderRadius:15,
      marginTop:5,
      marginBottom:5
    },
  }));
  const classes = useStyles();
  return (
    <div data-aos="zoom-in"
    data-aos-once="true">
      <div className={classes.root}>
        <Card className={classes.root} variant="outlined">
          <CardContent>
            <Typography
              className={classes.head}
              color="textSecondary"
              gutterBottom
            >
              {title} Request
            </Typography>
           <Divider orientation="horizontal"/>
           <br/>
            <Grid item xs={12} sm={12} direction="row" container marginTop="5">
              {" "}
              <Grid item xs={12} sm={3} align="left">
                <Avatar alt="avatar " src={imageUrl} />
              </Grid>
              <Grid item xs={12} sm={9} align="left">
                {" "}
                <Typography className={classes.name}  justify="center" >
                <div style={{ color: "",margin:"auto",justifyContent :"center"}}>{name}</div>
                </Typography>
                <br />
              </Grid>
            </Grid>
           
              <Typography align="center" className={classes.day}>              
     <br/>
                <div style={{ color: "",margin:"auto"}}> {view}</div>
               <div  style={{ color: "gray",margin:"auto"}}> {date}</div>
     <br/>
                           
              </Typography>{" "}
           
              
            <Typography className={classes} color="textSecondary" noWrap>
              {career}
            </Typography>

            <Button
              size="small"
              style={{ display: "flex", justifyContent: "flex-start" }}
            >
              LearnMore
            </Button>
           
            <br />
            <Typography variant="body2" component="p">
              Payment:{payment}
            </Typography>
          </CardContent>           
          <CardActions>
            <Button href="/RequestViewPage"size="small" style={{ color: "green" }}>
           
              Accept
            </Button>{" "}
            <Button size="small" style={{ color: "red" }}>
              {" "}
              Reject{" "}
            </Button>
          </CardActions>
        </Card>
      </div>
    </div>
  );
};

export default CoffeCard;
