// @ts-nocheck
import React from "react";
import {
  // TextField,
  Grid,
  // Button,
  Box,
  // Divider,
} from "@material-ui/core";

// import styles from "Assets/Styles/Offers/offersStyles.js";
import "Assets/Styles/Offers/offersStyles.css";

import SortList from "Components/Offers/sortList";
import OfferCard from "Components/Offers/offerCard";

export default function CardSide() {
  return (
    <Box
      // boxShadow={2}
      style={{
        /* backgroundColor: "#FFF", */ width: "72%",
        height: "100%",
      }}
    >
      <Grid
        container
        direction="row"
        justify="space-between"
        alignItems="center"
        style={{
          width: "85%",
          marginLeft: 80,
        }}
      >
        <SortList />

        <Grid
          className="resultsCount"
          item
          style={{
            fontSize: 15,
            fontWeight: "bold",
            fontFamily: "Roboto",
            color: "#625261",
            marginTop: 20,
            marginRight: 30,
          }}
        >
          {"15,000+ results"}
        </Grid>
      </Grid>

      <OfferCard />
    </Box>
  );
}
