// @ts-nocheck
import React from "react";
import {
  //   TextField,
  Grid,
  // ButtonBase,
  //   Button,
  //   Box,
  //   InputBase,
  //   Divider,
  // Collapse,
} from "@material-ui/core";
import styles from "Assets/Styles/Settings/settingsStyles.js";
import "Assets/Styles/Settings/settingsStyles.css";

export default function NavItems(props) {
  function IsActive() {
    if (props.isActive) {
      return (
        <Grid container justify="flex-end" style={styles.navItemActive}>
          {props.title}
        </Grid>
      );
    } else {
      return (
        <Grid container justify="flex-end" style={styles.navItem}>
          {props.title}
        </Grid>
      );
    }
  }

  return <IsActive />;
}
