import React from "react";
import { Grid, Typography, Paper } from "@material-ui/core";

export default function ScheduleEventsHeadings() {
  return (
    <Grid
      item
      xs={12}
      style={{
        display: "flex",
        justifyContent: "center",
      }}
    >
      <Grid
        item
        xs={10}
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          marginTop: 20,
        }}
      >
        <Typography
          style={{
            fontSize: 16,
            fontFamily: "Roboto",
            fontWeight: 800,
          }}
        >
          Scheduled Events
        </Typography>
        <Typography
          style={{
            fontSize: 12,
            fontFamily: "Roboto",
            fontWeight: 800,
          }}
        >
          View all
        </Typography>
      </Grid>
    </Grid>
  );
}
