// @ts-nocheck
import React from "react";
import { Grid, Divider } from "@material-ui/core";
import CustomizedSwitches from "Components/Settings/customizedSwitches";
import BillingTextInput from "Components/Settings/billingTextInput";
import SettingsCardNumber from "Components/Settings/settingsCardNumber";

import styles from "Assets/Styles/Settings/settingsStyles.js";

export default function SettingsCreditCardInfo() {
  return (
    <Grid>
      <Grid xs={12} sm={10} style={styles.settingPagesSubheading}>
        Add new card
      </Grid>
      <div style={{ marginTop: 20 }}></div>

      <SettingsCardNumber
            title="NAME ON CARD"
            placeholder="Nate Parrish"
            sm={10}
          />

      <Grid
        xs={12}
        sm={12}
        container
        style={{
          display: "flex",
          justifyContent: "center",
        }}
      >
        <Grid xs={12} sm={10} container>
          <SettingsCardNumber
            title="CARD NUMBER"
            placeholder="**** **** **** 2564"
            sm={4}
          />
          <SettingsCardNumber title="EXPIRY" placeholder="04/12" sm={2} />
          <SettingsCardNumber title="cvv" placeholder="256" sm={2} />
        </Grid>
      </Grid>
    </Grid>
  );
}
