import React from "react";
// import { TextField, Grid, Button, Box } from "@material-ui/core";

// import styles from "Assets/Styles/Offers/offersStyles.js";
import "Assets/Styles/Offers/offersStyles.css";

// import { Link as Link1 } from "react-router-dom";
// import commonStyles from "Assets/Styles/loginSignup/commonStyles";

// import imgOffers from "Assets/Images/preDashboard/imgOffers.svg";
// import imgFreelancers from "Assets/Images/preDashboard/imgFreelancers.svg";

import Select from "react-select";

export default function SortList() {
  const options = [
    { value: "Recommended", label: "Sort: Recommended" },
    { value: "Trending", label: "Sort: Trending" },
    { value: "Popularity", label: "Sort: Popularity" },
    { value: "Price: low to high", label: "Sort: Price: low to high" },
    { value: "Price: height to low", label: "Sort: Price: high to low" },
  ];

  const customStyles = {
    option: (provided, state) => ({
      ...provided,
      // borderBottom: "1px dotted pink",
      color: state.isSelected ? "whtie" : "black",
      // padding: 20,
    }),
    control: () => ({
      // none of react-select's styles are passed to <Control />
      width: 250,
    }),
    singleValue: (provided, state) => {
      const opacity = state.isDisabled ? 0.5 : 1;
      const transition = "opacity 300ms";

      return { ...provided, opacity, transition };
    },
  };

  return (
    <div
      style={{
        backgroundColor: "#FFF",
        color: "#000",
        height: 30,
        width: "19%",
        borderRadius: 5,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        paddingLeft: 105,
        marginLeft: 30,
        marginTop: 20,
        fontSize: 12,
        fontWeight: "bold",
      }}
    >
      <Select
        components={{
          DropdownIndicator: () => null,
          IndicatorSeparator: () => null,
        }}
        isSearchable={true}
        placeholder="Sort: Recommended"
        styles={customStyles}
        options={options}
      />
    </div>
  );
}
