import React from "react";
import {
  Typography,
  Button,
  Card,
  CardContent,
  CardActions,
  Avatar,
  Divider,Grid
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";


const CoffeCard = (props) => {
  const { title, name, payment, career, imageUrl, view, date } = props;
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      boxShadow:3,
      borderRadius:5,
      margin:1
    },
    paper: {
      padding: theme.spacing(2),
      margin: "auto",
      maxWidth: 1600,
    },
    image: {
      width: 128,
      height: 128,
    },
    img: {
      margin: "auto",
      display: "block",
      maxWidth: "100%",
      maxHeight: "100%",
    },
    head: {    fontSize: 15 ,fontFamily: "Roboto",fontWeight:500,marginBottom:5 },
    name: {  fontSize: 12 ,margin:"auto"},
    day: {
      fontSize: 14,
      backgroundColor: "#e1f2fc",
      borderRadius:15,
      marginTop:5,
      marginBottom:5
    },
  }));
  const classes = useStyles();
  return (
    <div >
      <div >
      <Grid  container
  direction="row"
  justify="flex-start"
  alignItems="center"><Typography justify="left" >
                <div style={{ color: "gray",margin:"auto",justifyContent :"left"}}>{date}</div>
                </Typography></Grid>
        <Grid className={classes.root} variant="outlined">
        
            {/* <Typography
              className={classes.head}
              color="textSecondary"
              gutterBottom
            >
              {title} Request
            </Typography> */}
         
          
            <Grid item container xs={12} sm={12} direction="row"  marginTop="5">
              {" "}
              <Grid item
  >
                <Avatar alt="avatar " src={imageUrl} />
                
              </Grid>
              <Grid item
  >
                
                <Typography 
style={{ color: "grey"}}  >
                {title}
                </Typography>
              </Grid>
            </Grid>
           
           
              
            
          
           
          
              
         
        </Grid>
      </div>
    </div>
  );
};

export default CoffeCard;
