// @ts-nocheck
import React from "react";
import {
    Grid,
    Divider,
    Avatar,
    Menu,
    MenuItem,
    Typography,
    Paper, Container
} from "@material-ui/core";

export default function MessageLabelCard(props) {
    return (
        <Grid item xs={12}>
            <Grid item xs={12} style={{ height: 60, alignItems: "center", display: "flex", marginTop: 5, marginBottom: 5 }} >

                <Grid item xs={3} style={{ height: 60, display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <Grid item style={{ height: 48, display: "flex", justifyContent: "center", alignItems: "center", backgroundColor: "white", borderRadius: "10%", width: 48 }}>
                        <img src={props.image} alt="msgImg"
                            style={{ height: 40, width: 40, borderRadius: "10%" }}
                        />
                    </Grid>
                </Grid>



                <Grid item xs={12} style={{ alignItems: "center", display: "flex" }}>
                    <Grid item xs={12} style={{ height: 40 }}>
                        <Grid item xs={12} style={{ display: "flex", alignItems: "flex-end" }}>
                            
                            <Grid item xs={8} style={{ height: 18, alignItems: "flex-end", display: "flex" }}>
                                <Typography style={{ marginLeft: 6, fontSize: 14, fontWeight: props.notReadYet ? 800 : 400 }}>{props.title}</Typography>
                            </Grid>

                            <Grid item xs={4} style={{ height: 18, alignItems: "center", display: "flex", justifyContent: "flex-end",  }}>
                                <Typography style={{ fontSize: 9, fontWeight: props.notReadYet ? 800 : 400, marginRight: 6 }}>{props.time}</Typography>
                            </Grid>
                        </Grid>

                        <Grid item xs={12} style={{ height: 22, display: "flex", alignItems: "center" }}>
                            <Typography style={{ fontSize: 13, fontWeight: 400, marginLeft: 6 }}>{props.body}</Typography>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            {!props.isLastItem ? <Grid item xs={12} ><Divider /></Grid> : <div />}
        </Grid>
    );
}